<template>
  <!-- <v-icon color="warning">mdi-swap-vertical</v-icon> -->
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn color="warning" variant="text" icon v-bind="activatorProps">
        <v-icon>mdi-swap-vertical</v-icon>
        <v-tooltip activator="parent" location="bottom"> Alterar Estação </v-tooltip>
      </v-btn>
    </template>
    <template v-slot:default="{}">
      <v-card class="mx-auto" width="400">
        <v-card-title class="bg-warning d-flex align-center"><v-icon class="mr-2">mdi-swap-vertical</v-icon><span>Alterar estação</span></v-card-title>
        <v-card-text
          ><v-form v-model="formValid">
            <v-row justify="center">
              <v-col cols="10">
                <v-autocomplete
                  v-model="datasource_id"
                  :items="datasources"
                  item-title="datasource_name"
                  item-value="datasource_id"
                  label="Estação meteorológica"
                  placeholder="Escolhe uma estação"
                  prepend-inner-icon="mdi-tower-fire"
                  no-data-text="Nenhuma estação encontrada"
                  color="secondary"
                  density="compact"
                  variant="outlined"
                  :rules="rules.required"
                  :loading="isLoadingDatasources"
                >
                  <template v-slot:item="{ props, item }">
                    <v-list-item
                      v-bind="props"
                      :subtitle="convertToKm(item.raw.distance)"
                    >
                    </v-list-item>
                  </template>
                </v-autocomplete>
              </v-col>
            </v-row> </v-form
        ></v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="dialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingEdit"
            :disabled="!formValid"
            @click="editClosestDatasource()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";

export default {
  name: "EditClosestDatasource",
  props: {
    region: {
      type: Object,
    },
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();

    return { generalStore, accountStore };
  },
  data() {
    return {
      dialog: false,
      datasource_id: null,
      formValid: false,
      rules: {
        required: [(v) => !!v || "Campo obrigatório"],
      },

      datasources: [],
      isLoadingDatasources: false,

      isLoadingEdit: false,
    };
  },
  watch: {
    dialog(value) {
      if (value) {
        this.onOpenDialog();
      }
    },
  },
  computed: {
    regionCenter() {
      const cordinates = this.region.geometry.geometry_shape.coordinates[0];
      const sum = cordinates.reduce(
        (acc, [lng, lat]) => [acc[0] + lng, acc[1] + lat],
        [0, 0]
      );
      const center = [sum[0] / cordinates.length, sum[1] / cordinates.length];
      return center;
    },
  },
  methods: {
    onOpenDialog() {
      this.datasource_id = this.region.closestdatasource_id;
      this.getClosestDatasources();
    },
    reset() {
      this.datasource_id = null;
      this.datasources = [];
      this.isLoadingDatasources = false;
    },
    async getClosestDatasources() {
      this.isLoadingDatasources = true;
      let payload = {
        location: this.regionCenter,
      };
      await axios
        .request({
          url: "api/closedatasources",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: payload,
        })
        .then((response) => {
          this.datasources = response.data.datasources;
          this.isLoadingDatasources = false;
        });
    },

    convertToKm(distance) {
      return (distance / 1000).toFixed(2) + " km";
    },

    editClosestDatasource() {
      this.isLoadingEdit = true;
      let payload = {
        datasource_id: this.datasource_id,
      };
      axios
        .request({
          url: `api/regions/${this.region.region_id}/closestdatasource`,
          method: "PATCH",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingEdit = false;
          this.dialog = false;
          this.generalStore.successSnackbar(
            "Estação da parcela alterada com sucesso."
          );
          this.$emit("refresh");
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingEdit = false;
          this.generalStore.errorSnackbar(
            "Erro ao alterar estação da parcela."
          );
        });
    },
  },
};
</script>

