<template>
  <v-sheet>
    <v-row v-if="isLoading" align="center">
      <v-col class="d-flex align-center justify-center">
        <v-progress-linear color="secondary" indeterminate></v-progress-linear>
      </v-col>
    </v-row>

    <v-row v-else-if="!isLoading && this.datasource != null">
      <v-col cols="6" class="d-flex flex-column justify-space-evenly">
        <span class="text-subtitle-1"
          >Nome: <b>{{ datasource?.datasource_name }}</b></span
        >
        <span class="text-subtitle-1">
          Tipo:
          <b>
            {{ isVirtual ? "Estação Virtual" : "Estação Física" }}
          </b>
        </span>
        <span class="text-subtitle-1"
          >Fornecedor:
          <b v-if="!isVirtual">{{
            datasource.dataprovider?.dataprovider_name
          }}</b>
          <b v-else>SISTEMA</b>
        </span>
        <span class="text-subtitle-1">
          ID de Fornecedor:
          <b>{{ datasource?.datasource_externalid }}</b>
        </span>
      </v-col>
      <v-col cols="6" v-if="datasource.datasource_location"
        ><PointMap
          :point="datasource.datasource_location"
          height="200px"
        ></PointMap
      ></v-col>
    </v-row>
  </v-sheet>
</template>

<script>
import PointMap from "@/components/MapUtils/PointMap.vue";

import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";

export default {
  name: "DatasourceDetail",
  components: { PointMap },
  props: {
    datasource_id: Number,
  },
  setup() {
    const accountStore = useAccountStore();

    return { accountStore };
  },
  data() {
    return {
      datasource: null,
      isLoading: false,
    };
  },
  computed: {
    isVirtual() {
      return this.datasource.dataprovider?.dataprovider_name == "SYSTEM";
    },
  },
  methods: {
    async getDatasource() {
      console.log(this.datasource_id);
      this.isLoading = true;
      await axios
        .request({
          url: "api/datasources/" + this.datasource_id,
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          console.log(response.data);
          this.datasource = response.data;
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  mounted() {
    this.getDatasource();
  },
  unmounted() {
    this.datasource = null;
  },
};
</script>

<style></style>
