<template>
  <v-card
    elevation="0"
    border
    rounded
    class="pa-5"
    height="85%"
    min-height="350"
  >
    <v-card-title class="d-flex justify-space-between align-center">
      <span class="text-h4">Informação Geral</span>
      <v-btn
        v-if="
          accountStore.hasRole(['Administrador', 'Organização', 'Produtor'])
        "
        color="warning"
        variant="text"
        icon
        size="large"
        @click="openEditRegionDialog"
      >
        <v-icon>mdi-pencil</v-icon>
        <v-tooltip activator="parent" location="bottom">
          Editar Detalhes</v-tooltip
        >
      </v-btn>
    </v-card-title>
    <v-card-text class="fill-height mt-5 scrollable-card">
      <v-sheet border rounded class="pa-5">
        <v-row>
          <v-col cols="12" class="d-flex align-center justify-space-between">
            <v-divider></v-divider><span class="text-h6 mx-2">Detalhes</span
            ><v-divider></v-divider
          ></v-col>
          <v-col cols="4">
            <span class="text-subtitle-1">
              Código da Parcela:
              <b>{{ region.region_code }}</b>
            </span>
          </v-col>
          <v-col cols="4">
            <span class="text-subtitle-1">
              Nome da Parcela: <b>{{ region.region_name }} </b>
            </span>
          </v-col>
          <v-col cols="4">
            <span class="text-subtitle-1">
              Tipo da Parcela:
              <b
                ><v-chip label color="info">{{
                  region.region_type.regiontype_name
                }}</v-chip>
              </b>
            </span>
          </v-col>

          <v-col cols="4">
            <span class="text-subtitle-1">
              Localidade: <b>{{ region.localidade }}</b>
            </span>
          </v-col>
          <v-col cols="4">
            <span class="text-subtitle-1">
              Cultura: <b>{{ region.crop?.crop_name || "Sem cultura" }}</b>
            </span>
          </v-col>
          <v-col cols="4">
            <span class="text-subtitle-1"
              >Organização:
              <v-chip color="warning">{{
                region.organization.organization_name
              }}</v-chip></span
            >
          </v-col>
          <v-col cols="12" class="d-flex flex-column">
            <!-- <span class="text-subtitle-1"> Descrição</span> -->
            <span v-if="region.region_description">{{
              region.region_description
            }}</span>
            <span v-else class="text-overline">Sem descrição</span>
          </v-col>
          <v-col cols="12" class="d-flex align-center justify-space-between">
            <v-divider></v-divider><span class="text-h6 mx-2">Meteorologia</span
            ><v-divider></v-divider
          ></v-col>

          <v-col cols="6" class="d-flex align-center">
            <span class="text-subtitle-1" full-width>
              Estação meteorológica
              <div class="d-flex align-center justify-space-around">
                <EditClosestDatasource
                  :region="region"
                  @refresh="() => $emit('refresh')"
                ></EditClosestDatasource>
                <div
                  v-if="region.closest_datasource"
                  class="d-flex align-center"
                >
                  <a
                    class="text-secondary text-decoration-none cursor-pointer"
                    @click="$refs.RegionMap.flyToStation"
                    >{{ region.closest_datasource?.datasource_name }}</a
                  >
                  <StationData
                    :station_id="region.closest_datasource.datasource_id"
                  ></StationData>
                </div>
                <span v-else class="text-overline">Sem estação</span>
              </div>
            </span>
          </v-col>
          <v-col cols="6">
            <span class="text-subtitle-1">
              Estação virtual
              <div v-if="region.virtual_datasource" class="d-flex align-center">
                <VirtualStationDialog
                  :region="region"
                  :editMode="true"
                  @refresh="() => $emit('refresh')"
                ></VirtualStationDialog>
                <b>{{ region.virtual_datasource?.datasource_name }}</b>
                <StationData
                  :station_id="region.virtual_datasource.datasource_id"
                ></StationData>
              </div>
              <div v-else class="d-flex align-center">
                <span class="text-overline">Sem estação</span>
                <VirtualStationDialog
                  :region="region"
                  :editMode="false"
                  @refresh="() => $emit('refresh')"
                ></VirtualStationDialog>
              </div>
            </span>
          </v-col>
        </v-row>
      </v-sheet>
      <br />
      <v-row class="fill-height">
        <v-col cols="12" class="fill-height">
          <RegionMap
            ref="RegionMap"
            :region="region"
            :zoom="15"
            height="100%"
          ></RegionMap>
        </v-col>
      </v-row>
    </v-card-text>
    <RegionEdit
      :regionProp="regionToEdit"
      :dialog="editDialog"
      @update:dialog="(value) => (editDialog = value)"
      @refresh="() => $emit('refresh')"
    ></RegionEdit>
  </v-card>
</template>

<script>
import RegionMap from "./RegionMap.vue";
import RegionEdit from "@/components/Regions/RegionEdit.vue";
import StationData from "../WeatherStations/StationData.vue";

import EditClosestDatasource from "./EditClosestDatasource.vue";
import VirtualStationDialog from "../WeatherStations/VirtualStationDialog.vue";

import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

export default {
  name: "RegionDetail",
  components: {
    RegionMap,
    RegionEdit,
    StationData,
    VirtualStationDialog,
    EditClosestDatasource,
  },
  props: {
    region: {
      type: Object,
    },
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();

    return { generalStore, accountStore };
  },
  data() {
    return {
      map: null,
      mapCenter: [-9, 39],

      editDialog: false,
      regionToEdit: {},
    };
  },
  computed: {
    eoisShapes() {
      let _shapes = this.region.elements_of_interest.filter(
        (eoi) => eoi.geometry !== null
      );
      return _shapes.map((eoi) => {
        return {
          type: "Feature",
          properties: {
            title: eoi.eoi_name,
            type: eoi.eoi_description,
          },
          geometry: eoi.geometry,
        };
      });
    },
    regionShape() {
      let _features = [
        {
          type: "Feature",
          properties: {},
          geometry: this.region.geometry.geometry_shape,
        },
      ];
      if (this.eoisShapes.length > 0) {
        _features = _features.concat(this.eoisShapes);
      }
      return {
        type: "FeatureCollection",
        features: _features,
      };
    },
  },
  methods: {
    openEditRegionDialog() {
      this.$emit("openEditRegionDialog");
      this.editDialog = true;
      this.regionToEdit = JSON.parse(JSON.stringify(this.region));
      this.regionToEdit.organization_id =
        this.region.organization.organization_id;
    },
  },
};
</script>

<style scoped>
.scrollable-card {
  max-height: 90%;
  overflow: auto;
}
</style>
