<template>
  <v-sheet class="pa-2" elevation="8" rounded="lg" width="100%">
    <v-data-table-virtual
      fixed-header
      no-data-text="Nenhuma parcela encontrada"
      loading-text="Carregando parcelas..."
      :headers="headers"
      :items="tableItems"
      :loading="isLoadingRegions"
      :height="tableHeight"
    >
      <template v-slot:item="row">
        <tr>
          <td>
            <v-btn
              color="info"
              variant="text"
              icon="mdi-dots-vertical-circle-outline"
              @click="showInfo(row.item.region_id)"
            ></v-btn>
          </td>

          <td>
            {{ row.item.region_code }}
          </td>
          <td>{{ row.item.region_name }}</td>
          <td>
            <v-chip label color="info">{{ row.item.regiontype }}</v-chip>
          </td>
          <td>{{ row.item.localidade }}</td>
          <td>{{ row.item.crop?.crop_name }}</td>
          <td v-if="accountStore.hasRole(['Administrador'])">
            <v-chip color="warning">{{ row.item.organization_name }}</v-chip>
          </td>
          <td
            v-if="
              accountStore.hasRole(['Administrador', 'Organização', 'Produtor'])
            "
            class="d-flex justify-center nowrap"
          >
            <v-btn
              color="warning"
              variant="text"
              icon
              @click="openEditDialog(row.item)"
            >
              <v-icon>mdi-pencil</v-icon>
              <v-tooltip activator="parent" location="bottom">
                Editar
              </v-tooltip>
            </v-btn>
            <v-btn
              v-if="accountStore.hasRole(['Administrador', 'Organização'])"
              color="error"
              variant="text"
              icon
              @click="openRemoveDialog(row.item)"
            >
              <v-icon>mdi-delete-outline</v-icon>
              <v-tooltip activator="parent" location="bottom">
                Remover
              </v-tooltip>
            </v-btn>
          </td>
        </tr>
      </template>
      <template v-if="hasMore && !isLoadingRegions" v-slot:[`body.append`]>
        <tr
          v-intersect.quiet="{
            handler: loadMore,
            options: {
              threshold: 0.5,
            },
          }"
        >
          <td :colspan="headers.length" class="text-center">
            <v-skeleton-loader type="table-row"></v-skeleton-loader>
          </td>
        </tr>
      </template>
    </v-data-table-virtual>
    <v-dialog
      v-model="deleteDialog"
      @afterLeave="region = null"
      width="auto"
      persistent
    >
      <v-card>
        <v-card-title class="bg-error d-flex align-center">
          <v-icon size="small" class="mr-2">mdi-delete-outline</v-icon>
          <span>Remover Parcela</span></v-card-title
        >
        <v-card-text>
          {{ region ? `Deseja remover a parcela ${region.region_name}?` : "" }}
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="deleteDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingDelete"
            @click="deleteRegion()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <RegionEdit
      :regionProp="region"
      :dialog="editDialog"
      @update:dialog="(value) => (editDialog = value)"
      @refresh="() => $emit('refresh')"
    ></RegionEdit>
  </v-sheet>
</template>

<script>
import RegionEdit from "@/components/Regions/RegionEdit.vue";

import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";
export default {
  name: "RegionsTable",
  components: {
    RegionEdit,
  },
  props: {
    regions: Array,
    isLoadingRegions: Boolean,
    hasMore: Boolean,
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();
    return { generalStore, accountStore };
  },
  data() {
    return {
      isLoading: false,
      editDialog: false,
      isLoadingDelete: false,
      deleteDialog: false,
      editFormValid: false,
      region: null,
    };
  },
  computed: {
    tableHeight() {
      return window.innerHeight - 350;
    },
    headers() {
      switch (this.accountStore.Role()) {
        case "Administrador":
          return [
            {
              title: "",
              align: "center",
            },

            {
              title: "Código da Parcela",
              key: "region_code",
            },
            {
              title: "Nome da Parcela",
              key: "region_name",
            },
            {
              title: "Tipo",
              key: "regiontype",
            },
            {
              title: "Localidade",
              key: "localidade",
            },
            {
              title: "Cultura",
              key: "crop",
            },
            {
              title: "Organização",
              key: "organization_name",
            },
            // {
            //   title: "Nº de EOIs",
            //   key: "elements_of_interest_count",
            // },
            {
              title: "",
              align: "center",
            },
          ];
        case "Agente de Campo":
          return [
            {
              title: "",
              align: "center",
            },
            {
              title: "Código da Parcela",
              key: "region_code",
            },
            {
              title: "Nome da Parcela",
              key: "region_name",
            },
            {
              title: "Tipo",
              key: "regiontype",
            },
            {
              title: "Localidade",
              key: "localidade",
            },
            {
              title: "Cultura",
              key: "crop",
            },
          ];
        default:
          return [
            {
              title: "",
              align: "center",
            },
            {
              title: "Código da Parcela",
              key: "region_code",
            },
            {
              title: "Nome da Parcela",
              key: "region_name",
            },
            {
              title: "Tipo",
              key: "regiontype",
            },
            {
              title: "Localidade",
              key: "localidade",
            },
            {
              title: "Cultura",
              key: "crop",
            },
            // {
            //   title: "Nº de EOIs",
            //   key: "elements_of_interest_count",
            // },
            {
              title: "",
              align: "center",
            },
          ];
      }
    },
    tableItems() {
      return this.regions;
    },
  },
  methods: {
    showInfo(region_id) {
      this.$emit("showInfo", region_id);
    },
    openEditDialog(item) {
      this.region = item;
      this.editDialog = true;
    },
    openRemoveDialog(item) {
      this.region = item;
      this.deleteDialog = true;
    },
    loadMore(isIntersecting) {
      if (isIntersecting) {
        // console.log("load more");
        this.$emit("loadMore");
      }
    },
    deleteRegion() {
      this.isLoadingDelete = true;
      axios
        .request({
          url: "api/regions/" + this.region.region_id,
          method: "delete",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.isLoadingDelete = false;
          this.deleteDialog = false;
          this.generalStore.successSnackbar("Parcela removida com sucesso.");
          this.$emit("refresh");
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingDelete = false;
          this.generalStore.errorSnackbar(error.response.data.error, 3000);
        });
    },
  },
};
</script>

<style></style>
