<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn color="info" icon="mdi-plus" v-bind="activatorProps"></v-btn>
    </template>
    <template v-slot:default="{}">
      <v-card class="mx-auto" min-width="400">
        <v-card-title class="bg-secondary"> Adicionar Protocolo </v-card-title>
        <v-card-text>
          <v-form v-model="formValid">
            <span class="text-subtitle-1 text-medium-emphasis">
              Protocolo
            </span>
            <v-autocomplete
              v-model="protocol"
              class="mt-4"
              :items="protocols"
              item-title="protocol_shortname"
              item-value="protocol_id"
              placeholder="Selecione um protocolo"
              color="secondary"
              density="compact"
              variant="outlined"
              :disabled="isLoadingProtocols"
              :rules="[(v) => !!v || 'Selecione um protocolo']"
            >
            </v-autocomplete>
          </v-form>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-between">
          <v-btn @click="dialog = false">Cancelar</v-btn>
          <v-btn
            color="info"
            variant="flat"
            @click="addProtocol"
            :loading="isLoadingPost"
            :disabled="!formValid"
          >
            Adicionar
          </v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "AddProtocolToPeriod",
  props: {
    region: Object,
    regioninperiod: Object,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      dialog: false,

      protocols: [],
      isLoadingProtocols: false,

      protocol: null,
      isLoadingPost: false,
      formValid: false,
    };
  },
  watch: {
    dialog(value) {
      if (value) {
        this.onOpenDialog();
      }
    },
  },
  methods: {
    onOpenDialog() {
      this.getProtocols();
    },
    reset() {
      this.protocols = [];
      this.isLoadingProtocols = false;
      this.protocol = null;
      this.isLoadingPost = false;
    },
    async getProtocols() {
      this.isLoadingProtocols = true;
      await axios
        .request({
          url: `api/protocols`,
          method: "GET",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
        })
        .then((response) => {
          this.protocols = response.data;
          this.isLoadingProtocols = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingProtocols = false;
          this.generalStore.errorSnackbar("Erro ao buscar protocolos");
        });
    },
    async addProtocol() {
      this.isLoadingPost = true;
      let payload = {
        regioninperiod_id: this.regioninperiod.regioninperiod_id,
        protocol_id: this.protocol,
      };
      console.log(payload);
      await axios
        .request({
          url: `api/protocolsinregions`,
          method: "POST",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingPost = false;
          this.generalStore.successSnackbar("Protocolo adicionado com sucesso");
          this.$emit("refresh");
          this.dialog = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingPost = false;
          this.generalStore.errorSnackbar("Erro ao adicionar protocolo");
        });
    },
  },
};
</script>

