<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn color="warning" variant="text" icon v-bind="activatorProps">
        <v-icon>mdi-pencil</v-icon>
        <v-tooltip activator="parent" location="bottom"> Editar </v-tooltip>
      </v-btn>
    </template>
    <template v-slot:default="{}">
      <v-card class="mx-auto" width="500">
        <v-card-title class="bg-warning d-flex align-center"
          ><v-icon size="small" class="mr-2">mdi-pencil</v-icon
          ><span>Editar EOI</span></v-card-title
        >
        <v-card-text>
          <v-skeleton-loader
            v-if="isLoadingEoi"
            type="text"
            class="mb-5"
          ></v-skeleton-loader>
          <v-form v-else v-model="formValid">
            <v-row>
              <v-col cols="6">
                <v-text-field
                  v-model.trim="eoi.eoi_name"
                  label="Nome"
                  placeholder="Insira o nome"
                  prepend-inner-icon="mdi-tag-text-outline"
                  density="compact"
                  variant="outlined"
                  :rules="[rules.required, rules.maxLength]"
                ></v-text-field>
              </v-col>
              <v-col cols="6">
                <v-select
                  v-model="eoi.eoitype_id"
                  :items="eoiTypes"
                  item-title="eoitype_name"
                  item-value="eoitype_id"
                  label="Tipo de EOI"
                  placeholder="Selecione um tipo"
                  prepend-inner-icon="mdi-map-marker-alert-outline"
                  no-data-text="Nenhum tipo de EOI encontrado"
                  density="compact"
                  variant="outlined"
                  :rules="[rules.required]"
                  :disabled="isLoadingTypes"
                ></v-select>
              </v-col>
              <v-col cols="12">
                <v-autocomplete
                  v-model="eoi.regioninperiod_id"
                  :items="regionPeriods"
                  item-title="period.period_name"
                  item-value="regioninperiod_id"
                  label="Período"
                  placeholder="Selecione um período"
                  prepend-inner-icon="mdi-calendar"
                  no-data-text="Nenhum período encontrado"
                  density="compact"
                  variant="outlined"
                  :disabled="isLoadingPeriods"
                >
                  <template v-slot:item="{ props, item }">
                    <v-list-item
                      v-bind="props"
                      :subtitle="
                        item.raw.period.period_name != 'Permanente'
                          ? formatDate(item.raw.period.period_startdate) +
                            ' - ' +
                            formatDate(item.raw.period.period_enddate)
                          : ''
                      "
                    ></v-list-item>
                  </template>
                </v-autocomplete>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  v-model.trim="eoi.eoi_description"
                  label="Descrição"
                  placeholder="Insira a descrição"
                  prepend-inner-icon="mdi-text-box-outline"
                  density="compact"
                  variant="outlined"
                  :rules="[rules.description]"
                ></v-textarea>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="dialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingEdit"
            :disabled="!formValid"
            @click="editEoi()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "EoiEdit",
  props: {
    eoi_id: Number,
    region: {
      type: Object,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      isLoadingEdit: false,

      dialog: false,
      eoi: null,
      isLoadingEoi: false,
      formValid: false,
      rules: {
        required: (v) => !!v || "Campo obrigatório",
        maxLength: (v) => (v && v.length <= 25) || "Máximo de 25 caracteres.",
        description: (v) =>
          (v && v.length <= 255) || !v || "Máximo de 255 caracteres.",
      },
      // EOI Types
      isLoadingTypes: false,
      eoiTypes: [],
      // Region Periods
      isLoadingPeriods: false,
      regionPeriods: [
        {
          regioninperiod_id: null,
          period: {
            period_id: null,
            period_name: "Permanente",
            period_startdate: null,
            period_enddate: null,
          },
        },
      ],
    };
  },
  watch: {
    dialog(val) {
      if (val) {
        this.onOpenDialog();
      }
    },
  },
  methods: {
    async onOpenDialog() {
      this.isLoadingEoi = true;
      await this.getEoi();
      await this.getEoiTypes();
      await this.getRegionPeriods();
      this.isLoadingEoi = false;
    },
    async getEoi() {
      this.isLoadingEoi = true;
      
      await axios
        .request({
          url: "api/eois/" + this.eoi_id,
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.eoi = {
            eoi_name: response.data.eoi_name,
            eoitype_id: response.data.element_of_interest_type.eoitype_id,
            regioninperiod_id: response.data.region_in_period?.regioninperiod_id,
            eoi_description: response.data.eoi_description,
          };
          this.isLoadingEoi = false;
        })
        .catch((error) => {
          this.isLoadingEoi = false;
          console.log(error);
        });
    },
    async editEoi() {
      this.isLoadingEdit = true;
      let payload = {
        eoitype_id: this.eoi.eoitype_id,
        regioninperiod_id: this.eoi.regioninperiod_id,
        eoi_name: this.eoi.eoi_name,
        eoi_description: this.eoi.eoi_description,
      };
      console.log(payload);
      await axios
        .request({
          url: "api/eois/" + this.eoi_id,
          method: "patch",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then(() => {
          this.isLoadingEdit = false;
          this.dialog = false;
          this.generalStore.successSnackbar("EOI editado com sucesso");
          this.$emit("refresh");
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingEdit = false;
          this.generalStore.errorSnackbar("Erro ao editar EOI");
        });
    },
    async getEoiTypes() {
      this.isLoadingTypes = true;
      await axios
        .request({
          url: "api/eoitypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.isLoadingTypes = false;
          this.eoiTypes = response.data;
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingTypes = false;
          this.generalStore.errorSnackbar("Erro ao buscar tipos de EOI");
        });
    },
    async getRegionPeriods() {
      this.isLoadingPeriods = true;
      await axios
        .request({
          url: "api/regions/" + this.region.region_id + "/regionsinperiods",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.isLoadingPeriods = false;
          this.regionPeriods.push(...response.data.regioninperiods);
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingPeriods = false;
          this.generalStore.errorSnackbar("Erro ao buscar períodos da região");
        });
    },

    formatDate(date) {
      return new Date(date).toLocaleDateString("pt-PT");
    },
    reset() {
      this.eoi = null;
      this.eoiTypes = [];
      this.isLoadingEdit = false;
      this.isLoadingEoi = false;
      this.regionPeriods = [
        {
          regioninperiod_id: null,
          period: {
            period_id: null,
            period_name: "Permanente",
            period_startdate: null,
            period_enddate: null,
          },
        },
      ];
    },
  },
};
</script>

<style></style>
