<template>
  <v-dialog v-model="localDialog" @afterLeave="reset()" width="auto">
    <v-card class="mx-auto" min-width="500">
      <v-card-title class="bg-secondary"
        ><span> Protocolos </span>
      </v-card-title>
      <v-card-text>
        <v-row>
          <v-col cols="10">
            <span class="text-subtitle-1">
              Parcela: <b>{{ region.region_name }} </b>
            </span>
            <br />
            <span class="text-subtitle-1">
              Periodo: <b>{{ regioninperiod.name }} </b>
            </span>
          </v-col>
          <v-col cols="2">
            <AddProtocolToPeriod
              v-if="
                accountStore.hasRole([
                  'Administrador',
                  'Organização',
                  'Produtor',
                ])
              "
              :region="region"
              :regioninperiod="regioninperiod"
              @refresh="getProtocols()"
            ></AddProtocolToPeriod>
          </v-col>
        </v-row>
        <v-row>
          <v-sheet class="pa-2 scrollable" border rounded>
            <v-data-table-virtual
              :headers="headers"
              :items="protocols"
              :loading="isLoadingProtocols"
              no-data-text="Sem protocolos"
            >
              <template v-slot:item="row">
                <tr>
                  <td>{{ row.item.protocol_shortname }}</td>
                  <td class="text-end">
                    <v-btn
                      v-if="
                        accountStore.hasRole([
                          'Produtor',
                          'Organização',
                          'Administrador',
                        ])
                      "
                      color="red"
                      variant="text"
                      icon
                      @click="deleteProtocolInPeriod(row.item)"
                    >
                      <v-icon>mdi-delete-outline</v-icon>
                      <v-tooltip activator="parent" location="bottom">
                        Remover
                      </v-tooltip>
                    </v-btn>
                  </td>
                </tr>
              </template>
            </v-data-table-virtual>
          </v-sheet>
        </v-row>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import AddProtocolToPeriod from "./AddProtocolToPeriod.vue";

import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";

export default {
  name: "ProtocolsinPeriod",
  components: {
    AddProtocolToPeriod,
  },
  props: {
    region: {
      type: Object,
    },
    regioninperiod: {
      type: Object,
      required: true,
    },
    dialog: {
      type: Boolean,
      required: true,
    },
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();

    return { generalStore, accountStore };
  },
  data() {
    return {
      protocols: [],
      isLoadingProtocols: false,
      headers: [
        {
          title: "Nome do Protocolo",
        },
        {
          title: "",
        },
      ],
      isLoadingDelete: false,
    };
  },
  watch: {
    dialog(value) {
      if (value) {
        this.onOpenDialog();
      }
    },
  },
  computed: {
    localDialog: {
      get() {
        return this.dialog;
      },
      set(value) {
        this.$emit("update:dialog", value);
      },
    },
  },
  methods: {
    onOpenDialog() {
      console.log(this.regioninperiod);
      this.getProtocols();
    },
    reset() {
      this.protocols = [];
    },
    async getProtocols() {
      this.isLoadingProtocols = true;
      await axios
        .request({
          url: `api/regionsinperiods/${this.regioninperiod.regioninperiod_id}/protocolsinregions`,
          method: "GET",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
        })
        .then((response) => {
          this.isLoadingProtocols = false;
          this.protocols = response.data.protocols;
        })
        .catch((error) => {
          this.isLoadingProtocols = false;
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao carregar protocolos.");
        });
    },
    async deleteProtocolInPeriod(protocol) {
      this.isLoadingDelete = true;
      console.log(protocol);
      await axios
        .request({
          url: `api/protocolsinregions/${protocol.protocolonregioninperiod_id}`,
          method: "DELETE",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
        })
        .then(() => {
          this.isLoadingDelete = false;
          this.generalStore.successSnackbar("Protocolo removido com sucesso.");
          this.getProtocols();
        })
        .catch((error) => {
          this.isLoadingDelete = false;
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao remover protocolo.");
        });
    },
  },
};
</script>

<style scoped>
.scrollable {
  max-height: 300px;
  width: 100%;
  overflow-y: auto;
}
</style>
