<template>
  <v-card
    elevation="0"
    border
    rounded
    class="pa-5"
    height="85%"
    min-height="350"
  >
    <v-card-title>
      <span class="text-h4">Pesquisa</span>
      <v-row class="d-flex align-center justify-space-between">
        <v-col cols="3"
          ><v-text-field
            v-model="filter.eoi_name"
            color="secondary"
            placeholder="Nome"
            density="compact"
            variant="solo"
            hide-details
            class="mt-5"
            clearable
            @click:clear="searchEOIs"
          ></v-text-field
        ></v-col>
        <v-col cols="6"
          ><v-autocomplete
            v-model="filter.regioninperiod_id"
            :items="regionPeriods"
            item-title="period.period_name"
            item-value="regioninperiod_id"
            color="secondary"
            placeholder="Período"
            density="compact"
            variant="solo"
            hide-details
            class="mt-5"
            clearable
            append-icon="mdi-magnify"
            :menu-props="{ 'max-height': '200px' }"
            @click:clear="searchEOIs"
            @click:append="searchEOIs"
          >
            <template v-if="hasMorePeriods" v-slot:append-item>
              <div v-intersect.quiet="loadMorePeriods">
                <v-skeleton-loader type="list-item"></v-skeleton-loader>
              </div>
            </template>
            <template v-slot:item="{ props, item }">
              <v-list-item
                v-bind="props"
                :subtitle="
                  item.raw.period.period_name != 'Permanente'
                    ? formatDate(item.raw.period.period_startdate) +
                      ' - ' +
                      formatDate(item.raw.period.period_enddate)
                    : ''
                "
              ></v-list-item>
            </template> </v-autocomplete
        ></v-col>
        <v-col cols="3" class="d-flex justify-end"
          ><AddEoi
            v-if="
              accountStore.hasRole(['Produtor', 'Organização', 'Administrador'])
            "
            :region="region"
            class="mb-5"
            @refreshEois="searchEOIs()"
          ></AddEoi
        ></v-col>
      </v-row>
    </v-card-title>
    <v-card-text class="scrollable-card mt-5">
      <v-progress-linear
        v-if="isLoading"
        color="primary"
        indeterminate
      ></v-progress-linear>
      <v-data-iterator
        v-else
        :items="eois"
        item-value="eoi_id"
        :items-per-page="eois.length"
      >
        <template v-slot:default="{ items, isExpanded, toggleExpand }">
          <v-row>
            <v-col cols="12" v-for="item in items" :key="item.raw.eoi_id">
              <v-card>
                <v-card-title class="d-flex align-center justify-space-between">
                  <div class="d-flex align-center">
                    <v-icon color="secondary" start>{{
                      getEoiIcon(item.raw)
                    }}</v-icon>

                    <h4>
                      {{ item.raw.eoi_name }}
                    </h4>
                  </div>
                  <div
                    v-if="
                      accountStore.hasRole([
                        'Produtor',
                        'Organização',
                        'Administrador',
                      ])
                    "
                  >
                    <EoiEdit
                      :eoi_id="item.raw.eoi_id"
                      :region="region"
                      @refresh="searchEOIs()"
                    ></EoiEdit>
                    <v-btn
                      color="error"
                      variant="text"
                      icon
                      @click="openDeleteDialog(item.raw)"
                    >
                      <v-icon>mdi-delete-outline</v-icon>
                      <v-tooltip activator="parent" location="bottom">
                        Remover
                      </v-tooltip>
                    </v-btn>
                  </div>
                </v-card-title>
                <div class="px-4">
                  <v-switch
                    :label="`${
                      isExpanded(item) ? 'Esconder' : 'Mostrar'
                    } Detalhes`"
                    :model-value="isExpanded(item)"
                    density="compact"
                    color="secondary"
                    @click="() => toggleExpand(item)"
                  ></v-switch>
                  <v-expand-transition>
                    <div v-if="isExpanded(item)" class="mb-5">
                      <EoiDetail :eoi_id="item.raw.eoi_id"></EoiDetail>
                    </div>
                  </v-expand-transition>
                </div>
              </v-card>
            </v-col>
            <v-col cols="12" v-if="hasMore">
              <v-skeleton-loader
                v-intersect.quiet="{
                  handler: loadMore,
                  options: {
                    threshold: 0.3,
                  },
                }"
                type="card"
              ></v-skeleton-loader>
            </v-col>
            <v-col v-else cols="12">
              <br />
            </v-col>
          </v-row>
        </template>
      </v-data-iterator>
      <v-dialog
        v-model="deleteDialog"
        persistent
        width="auto"
        @afterLeave="resetDeleteDialog"
      >
        <v-card width="40rem">
          <v-card-title class="bg-error d-flex align-center"
            ><v-icon size="small" class="mr-2">mdi-delete-outline</v-icon
            >Remover elemento de interesse da região</v-card-title
          >
          <v-card-text>
            <span
              >Tem a certeza que quer remover
              <b>{{ eoiToDelete.eoi_type }}</b> com o nome
              <b>{{ eoiToDelete.eoi_name }}</b
              >?
            </span>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions class="d-flex justify-space-around">
            <v-btn
              color="error"
              variant="tonal"
              icon="mdi-close"
              @click="deleteDialog = false"
            ></v-btn>
            <v-btn
              color="secondary"
              variant="tonal"
              icon="mdi-check"
              :loading="isLoadingDelete"
              @click="deleteEOI()"
            ></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-card-text>
  </v-card>
</template>

<script>
import EoiDetail from "./EoiDetail.vue";
import AddEoi from "./AddEoi.vue";
import EoiEdit from "./EoiEdit.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "EOIsDetail",
  components: { EoiDetail, AddEoi, EoiEdit },
  props: {
    region: {
      type: Object,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      //List of EOIs
      isLoading: false,
      eois: [],
      limit: 5,
      offset: 0,
      hasMore: false,
      filter: {
        eoi_name: null,
        regioninperiod_id: null,
      },

      // list of region periods
      regionPeriods: [
        {
          regioninperiod_id: 0,
          period: {
            period_name: "Permanente",
          },
        },
      ],
      isPeriodsLoading: false,
      limitPeriods: 5,
      offsetPeriods: 0,
      hasMorePeriods: false,
      // Delete EOI
      deleteDialog: false,
      eoiToDelete: {
        eoi_id: 0,
        eoi_name: "",
        eoi_type: "",
      },
      isLoadingDelete: false,
    };
  },
  computed: {},
  methods: {
    formatDate(date) {
      return new Date(date).toLocaleDateString("pt-PT");
    },
    async getRegionPeriods() {
      this.isPeriodsLoading = true;
      await axios
        .request({
          url: "api/regions/" + this.region.region_id + "/regionsinperiods",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            limit: this.limitPeriods,
            offset: this.offsetPeriods,
          },
        })
        .then((response) => {
          let _periods = response.data.regioninperiods;
          this.regionPeriods.push(..._periods);
          this.offsetPeriods += _periods.length;
          this.hasMorePeriods = response.data.hasMore;
          this.isPeriodsLoading = false;
        })
        .catch((error) => {
          console.error(error);
          this.isPeriodsLoading = false;
        });
    },
    loadMorePeriods(isIntersecting) {
      if (isIntersecting) {
        this.getRegionPeriods();
      }
    },
    async getRegionEOIs() {
      await axios
        .request({
          url: "api/regions/" + this.region.region_id + "/eois",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            limit: this.limit,
            offset: this.offset,

            //search filter
            eoi_name: this.filter.eoi_name,
            regioninperiod_id: this.filter.regioninperiod_id,
          },
        })
        .then((response) => {
          let _eois = response.data.eois;
          this.eois.push(..._eois);
          this.offset += _eois.length;
          this.hasMore = response.data.hasMore;
        })
        .catch((error) => {
          console.error(error);
        });
    },
    loadMore(isIntersecting) {
      if (isIntersecting) {
        this.getRegionEOIs();
      }
    },
    getEoiIcon(eoi) {
      switch (eoi.element_of_interest_type.eoitype_name) {
        case "Árvore":
          return "mdi-tree";
        case "Linha":
          return "mdi-vector-line";
        case "Entrada":
          return "mdi-gate";
        case "Lago":
          return "mdi-water";
        case "Armadilha":
          return "mdi-bug";
        default:
          return "mdi-help-circle";
      }
    },
    openDeleteDialog(eoi) {
      this.eoiToDelete = {
        eoi_id: eoi.eoi_id,
        eoi_name: eoi.eoi_name,
        eoi_type: eoi.element_of_interest_type.eoitype_name,
      };
      this.deleteDialog = true;
    },

    resetDeleteDialog() {
      this.eoiToDelete = {
        eoi_id: 0,
        eoi_name: "",
        eoi_type: "",
      };
    },
    deleteEOI() {
      this.isLoadingDelete = true;
      axios
        .request({
          url:
            "api/regions/" +
            this.region.region_id +
            "/eois/" +
            this.eoiToDelete.eoi_id,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.isLoadingDelete = false;
          this.generalStore.successSnackbar(
            "Elemento de interesse removido com sucesso"
          );
          this.searchEOIs();
          this.deleteDialog = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingDelete = false;
          this.generalStore.errorSnackbar(error.response.data.error, 3000);
        });
    },
    searchEOIs() {
      this.eois = [];
      this.offset = 0;
      this.hasMore = false;
      this.getRegionEOIs();
    },
    refreshEOIs() {
      console.log("refreshing EOIs");
      this.eois = [];
      this.offset = 0;
      this.hasMore = true;
      this.getRegionEOIs();
      this.regionPeriods = [
        {
          regioninperiod_id: 0,
          period: {
            period_name: "Permanente",
          },
        },
      ];
      this.offsetPeriods = 0;
      this.hasMorePeriods = true;
      this.getRegionPeriods();
    },
  },
  mounted() {
    this.isLoading = true;
    this.getRegionEOIs().then(() => {
      this.isLoading = false;
    });
    this.getRegionPeriods();
  },
  unmounted() {
    this.eois = [];
    this.filter = {
      eoi_name: null,
    };
  },
};
</script>

<style scoped>
.scrollable-card {
  max-height: 90%;
  overflow: auto;
}
</style>
