<template>
  <v-dialog v-model="localDialog" @afterLeave="reset()" width="auto">
    <v-card class="mx-auto" min-width="500">
      <v-card-title class="bg-secondary">Inimigos a Estudar</v-card-title>
      <v-card-text>
        <v-row>
          <v-col cols="10">
            <span class="text-subtitle-1">
              Parcela: <b>{{ region.region_name }} </b>
            </span>
            <br />
            <span class="text-subtitle-1">
              Periodo: <b>{{ regioninperiod.name }} </b>
            </span>
          </v-col>
          <v-col cols="2">
            <AddEnemyToPeriod
              v-if="
                accountStore.hasRole([
                  'Administrador',
                  'Organização',
                  'Produtor',
                ])
              "
              :region="region"
              :regioninperiod="regioninperiod"
              :existingEnemies="existingEnemies"
              @refresh="getEnemies()"
            ></AddEnemyToPeriod>
          </v-col>
          <v-row>
            <v-sheet class="pa-2 ma-2 scrollable" border rounded>
              <v-data-table-virtual
                :headers="headers"
                :items="enemies"
                :loading="isLoadingEnemies"
                no-data-text="Sem inimigos"
              >
                <template v-slot:item="row">
                  <tr>
                    <td>{{ row.item.enemy.enemy_name }}</td>
                    <td class="text-end">
                      <v-btn
                        v-if="
                          accountStore.hasRole([
                            'Produtor',
                            'Organização',
                            'Administrador',
                          ])
                        "
                        color="error"
                        variant="text"
                        :disabled="isLoadingDelete"
                        icon
                        @click="deleteEnemyInPeriod(row.item)"
                      >
                        <v-icon>mdi-delete-outline</v-icon>
                        <v-tooltip activator="parent" location="bottom">
                          Remover
                        </v-tooltip>
                      </v-btn>
                    </td>
                  </tr>
                </template>
              </v-data-table-virtual>
            </v-sheet>
          </v-row>
        </v-row>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import AddEnemyToPeriod from "./AddEnemyToPeriod.vue";

import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";

export default {
  name: "EnemiesinPeriod",
  components: {
    AddEnemyToPeriod,
  },
  props: {
    region: {
      type: Object,
    },
    regioninperiod: {
      type: Object,
      required: true,
    },
    dialog: {
      type: Boolean,
      required: true,
    },
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();

    return { generalStore, accountStore };
  },
  data() {
    return {
      enemies: [],
      isLoadingEnemies: false,
      headers: [
        {
          title: "Inimigo",
        },
        {
          title: "",
        },
      ],
      isLoadingDelete: false,
    };
  },
  watch: {
    dialog(value) {
      if (value) {
        this.onOpenDialog();
      }
    },
  },
  computed: {
    localDialog: {
      get() {
        return this.dialog;
      },
      set(value) {
        this.$emit("update:dialog", value);
      },
    },
    existingEnemies() {
      return this.enemies.map((enemy) => enemy.enemy);
    },
  },
  methods: {
    onOpenDialog() {
      this.getEnemies();
    },
    reset() {
      this.enemies = [];
    },
    async getEnemies() {
      this.isLoadingEnemies = true;
      await axios
        .request({
          url: `api/regionsinperiods/${this.regioninperiod.regioninperiod_id}/enemiesmonitored`,
          method: "GET",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
        })
        .then((response) => {
          this.isLoadingEnemies = false;
          this.enemies = response.data;
          console.log(this.enemies);
        })
        .catch((error) => {
          this.isLoadingEnemies = false;
          console.log(error);
          this.generalStore.errorSnackbar("Erro ao buscar inimigos");
        });
    },
    async deleteEnemyInPeriod(enemy) {
      console.log(enemy);
      this.isLoadingDelete = true;
      await axios
        .request({
          url: `api/enemiesmonitored/${enemy.enemymonitored_id}`,
          method: "DELETE",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
        })
        .then(() => {
          this.isLoadingDelete = false;
          this.generalStore.successSnackbar("Inimigo removido com sucesso");
          this.getEnemies();
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingDelete = false;
          this.generalStore.errorSnackbar("Erro ao remover inimigo");
        });
    },
  },
};
</script>

<style scoped>
.scrollable {
  max-height: 300px;
  width: 100%;
  overflow-y: auto;
}
</style>
