<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn color="success" icon="mdi-plus" v-bind="activatorProps"></v-btn>
    </template>
    <template v-slot:default="{}">
      <v-card class="mx-auto" min-width="400">
        <v-card-title class="bg-success">Adicionar Inimigo</v-card-title>
        <v-card-text>
          <v-form v-model="formValid">
            <!-- <span class="text-subtitle-1 text-medium-emphasis">Inimigo</span> -->
            <v-autocomplete
              v-model="enemy"
              class="mt-4"
              label="Inimigo"
              placeholder="Selecione um inimigo"
              prepend-inner-icon="mdi-bug"
              :items="selectItems"
              item-title="enemy_name"
              item-value="enemy_id"
              density="compact"
              variant="outlined"
              :loading="isLoadingEnemies"
              :rules="[(v) => !!v || 'Campo obrigatório']"
            ></v-autocomplete>
          </v-form>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-between">
          <v-btn color="error" variant="tonal" @click="dialog = false"
            >Cancelar</v-btn
          >
          <v-btn
            color="success"
            variant="flat"
            @click="postEnemy"
            :loading="isLoadingPost"
            :disabled="!formValid"
          >
            Adicionar
          </v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "AddEnemyToPeriod",
  props: {
    region: Object,
    regioninperiod: Object,
    existingEnemies: Array,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      dialog: false,
      enemies: [],
      isLoadingEnemies: false,
      enemy: null,
      isLoadingPost: false,
      formValid: false,
    };
  },
  watch: {
    dialog(value) {
      if (value) {
        this.onOpenDialog();
      }
    },
  },
  computed: {
    selectItems() {
      let enemies = this.enemies.filter((enemy) => enemy.enemytype_id != 1); //HARD CODED 1 = Not applicable
      return enemies.filter(
        (enemy) =>
          !this.existingEnemies.some(
            (existingEnemy) => existingEnemy.enemy_id === enemy.enemy_id
          )
      );
    },
  },
  methods: {
    onOpenDialog() {
      this.getEnemies();
    },
    reset() {
      this.enemies = [];
      this.isLoadingEnemies = false;
      this.enemy = null;
      this.isLoadingPost = false;
    },
    async getEnemies() {
      this.isLoadingEnemies = true;
      await axios
        .request({
          url: `api/enemies`,
          method: "GET",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
        })
        .then((response) => {
          this.enemies = response.data;
          this.isLoadingEnemies = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingEnemies = false;
          this.generalStore.errorSnackbar("Erro ao buscar inimigos");
        });
    },
    async postEnemy() {
      this.isLoadingPost = true;
      let payload = {
        enemy_id: this.enemy,
        regioninperiod_id: this.regioninperiod.regioninperiod_id,
      };
      console.log(payload);
      await axios
        .request({
          url: `api/enemiesmonitored`,
          method: "POST",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
          data: payload,
        })
        .then(() => {
          this.isLoadingPost = false;
          this.dialog = false;
          this.$emit("refresh");
          this.generalStore.successSnackbar("Inimigo adicionado com sucesso");
        })
        .catch((error) => {
          this.isLoadingPost = false;
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao adicionar inimigo");
        });
    },
  },
};
</script>

<style></style>
