<template>
  <v-sheet>
    <v-row v-if="isLoading" align="center">
      <v-col class="d-flex align-center justify-center">
        <v-progress-linear color="secondary" indeterminate></v-progress-linear>
      </v-col>
    </v-row>
    <v-row v-else-if="!isLoading && this.eoi != null">
      <v-col cols="6" class="d-flex flex-column justify-space-evenly">
        <div class="d-flex flex-column justify-space-evenly">
          <span class="text-subtitle-1"
            >Nome: <b>{{ eoi.eoi_name }}</b></span
          >
          <span class="text-subtitle-1"
            >Tipo: <b>{{ eoi.element_of_interest_type.eoitype_name }}</b></span
          >
          <span class="text-subtitle-1">
            Descrição:
            <b>{{
              eoi.eoi_description ? eoi.eoi_description : "Sem descrição"
            }}</b>
          </span>
        </div>

        <div v-if="eoi.region_in_period" class="d-flex flex-column">
          <v-divider></v-divider>
          <br />
          <span class="text-h6">
            {{ eoi.region_in_period.period.period_type.periodtype_name }}</span
          >
          <br />
          <span class="text-subtitle-1"
            >Nome: <b>{{ eoi.region_in_period.period.period_name }} </b></span
          >
          <span class="text-subtitle-1"
            >Data Início:
            <b>{{
              formatDate(eoi.region_in_period.period.period_startdate)
            }}</b></span
          >
          <span class="text-subtitle-1"
            >Data Fim:
            <b>{{
              formatDate(eoi.region_in_period.period.period_enddate)
            }}</b></span
          >
        </div>
      </v-col>
      <v-col cols="6">
        <v-row>
          <v-col cols="12" v-if="eoi.geometry">
            <div v-if="eoi.geometry.geometry_shape.type == 'Polygon'">
              <PolyMap
                :polygon="eoi.geometry.geometry_shape"
                height="300px"
              ></PolyMap>
            </div>
            <div v-else-if="eoi.geometry.geometry_shape.type == 'LineString'">
              <LineMap
                :line="eoi.geometry.geometry_shape"
                height="300px"
              ></LineMap>
            </div>
            <div v-else-if="eoi.geometry.geometry_shape.type == 'Point'">
              <PointMap
                :point="eoi.geometry.geometry_shape"
                height="300px"
              ></PointMap>
            </div>
          </v-col>
          <v-col v-else>
            <v-row align="center">
              <v-col class="d-flex align-center justify-center">
                <v-icon size="100">mdi-map-marker-off</v-icon>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-sheet>
</template>

<script>
import PolyMap from "@/components/MapUtils/PolyMap.vue";
import LineMap from "@/components/MapUtils/LineMap.vue";
import PointMap from "@/components/MapUtils/PointMap.vue";

import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";

export default {
  name: "EoiDetail",
  components: { PolyMap, LineMap, PointMap },
  props: {
    eoi_id: Number,
  },
  setup() {
    const accountStore = useAccountStore();

    return { accountStore };
  },
  data() {
    return {
      eoi: null,
      isLoading: false,
    };
  },
  methods: {
    async getEoi() {
      this.isLoading = true;
      await axios
        .request({
          url: "api/eois/" + this.eoi_id,
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.eoi = response.data;
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    formatDate(date) {
      return new Date(date).toLocaleDateString("pt-PT");
    },
    reset() {
      this.eoi = null;
    },
  },
  mounted() {
    this.getEoi();
  },
  unmounted() {
    this.reset();
  },
};
</script>

<style></style>
