<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn color="success" icon="mdi-plus" v-bind="activatorProps"> </v-btn>
    </template>
    <template v-slot:default="{}">
      <v-card class="mx-auto" min-width="500">
        <v-card-title class="bg-success">{{ steps[step].title }}</v-card-title>
        <v-window v-model="step">
          <v-window-item :value="0">
            <v-card-text>
              <v-row justify="center">
                <v-col cols="10">
                  <v-form v-model="steps[step].formValid">
                    <!-- <span class="text-subtitle-1 text-medium-emphasis">
                    Tipo de Período*
                  </span> -->
                    <v-autocomplete
                      v-model="periodTypeSelected"
                      class="mt-4"
                      :items="periodTypes"
                      label="Tipo de Período*"
                      placeholder="Selecione um tipo"
                      no-data-text="Sem resultados"
                      item-title="periodtype_name"
                      item-value="periodtype_id"
                      density="compact"
                      variant="outlined"
                      clearable
                      :disabled="isLoadingTypes"
                      :loading="isLoadingPeriods"
                      :rules="[(v) => !!v || 'Selecione um tipo']"
                    ></v-autocomplete> </v-form
                ></v-col>
              </v-row>
            </v-card-text>
          </v-window-item>
          <v-window-item :value="1">
            <v-card-text>
              <v-row justify="center">
                <v-col cols="10">
                  <v-form v-model="steps[step].formValid">
                    <!-- <span class="text-subtitle-1 text-medium-emphasis">
                  Período*
                </span> -->
                    <v-autocomplete
                      v-model="regioninperiod.period_id"
                      class="mt-4"
                      :items="selectPeriodItems"
                      label="Período*"
                      placeholder="Selecione um período"
                      no-data-text="Sem resultados"
                      item-title="period_name"
                      item-value="period_id"
                      density="compact"
                      variant="outlined"
                      clearable
                      :disabled="isLoadingPeriods"
                      :rules="[(v) => !!v || 'Selecione um período']"
                    >
                      <template v-slot:item="{ props, item }">
                        <v-list-item
                          v-bind="props"
                          :subtitle="
                            formatDate(item.raw.period_startdate) +
                            ' - ' +
                            formatDate(item.raw.period_enddate)
                          "
                        ></v-list-item>
                      </template>
                    </v-autocomplete>
                  </v-form>
                </v-col>
              </v-row>
            </v-card-text>
          </v-window-item>
        </v-window>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn
            v-if="step > 0"
            prepend-icon="mdi-chevron-left"
            color="info"
            variant="tonal"
            @click="step--"
          >
            Anterior
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            v-if="step < 1"
            :disabled="!steps[step].formValid"
            append-icon="mdi-chevron-right"
            color="info"
            variant="tonal"
            @click="step++"
          >
            Próximo
          </v-btn>
          <v-btn
            v-else-if="step === 1"
            :disabled="!steps[step].formValid"
            append-icon="mdi-check"
            color="success"
            variant="flat"
            :loading="isLoadingPost"
            @click="postRegionPeriod()"
          >
            Adicionar
          </v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "AddRegionPeriod",
  props: {
    region: Object,
    regioninperiods: Array,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      dialog: false,
      step: 0,
      steps: [
        { title: "Tipo de Período", formValid: false },
        { title: "Período", formValid: false },
      ],
      periodTypes: [],
      periodTypeSelected: null,
      periods: [],
      regioninperiod: {
        region_id: this.region.region_id,
        period_id: null,
      },
      isLoadingTypes: false,
      isLoadingPeriods: false,
      isLoadingPost: false,
    };
  },
  watch: {
    dialog(val) {
      if (val) {
        this.onOpenDialog();
      }
    },
    periodTypeSelected(val) {
      if (val) {
        this.getPeriods();
      }
    },
  },
  computed: {
    selectPeriodItems() {
      return this.periods.filter(
        (period) => !this.existingPeriods.includes(period.period_id)
      );
    },
    existingPeriods() {
      return this.regioninperiods.map((regioninperiod) => {
        return regioninperiod.period_id;
      });
    },
  },
  methods: {
    onOpenDialog() {
      this.getPeriodTypes();
    },
    async postRegionPeriod() {
      this.isLoadingPost = true;
      console.log(this.regioninperiod);
      await axios
        .request({
          url: "api/regionsinperiods",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: this.regioninperiod,
        })
        .then((response) => {
          this.isLoadingPost = false;
          this.generalStore.successSnackbar("Período adicionado com sucesso");
          this.$emit("refreshRegionPeriods");
          this.dialog = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao adicionar período");
          this.isLoadingPost = false;
        });
    },
    async getPeriodTypes() {
      this.isLoadingTypes = true;
      await axios
        .request({
          url: "api/periodtypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            periodtype_id: this.periodTypeSelected,
          },
        })
        .then((response) => {
          this.periodTypes = response.data;
          this.isLoadingTypes = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao buscar tipos de período");
          this.isLoadingTypes = false;
        });
    },
    async getPeriods() {
      this.isLoadingPeriods = true;
      await axios
        .request({
          url: "api/periods",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.periods = response.data.periods;
          this.isLoadingPeriods = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao buscar períodos");
          this.isLoadingPeriods = false;
        });
    },
    formatDate(date) {
      return new Date(date).toLocaleDateString("pt-PT");
    },
    reset() {
      this.regioninperiod = {
        region_id: this.region.region_id,
        period_id: null,
      };
      this.periodTypeSelected = null;
      this.periodTypes = [];
      this.periods = [];
      this.isLoadingTypes = false;
      this.isLoadingPeriods = false;
      this.isLoadingPost = false;
      this.step = 0;
    },
  },
};
</script>

<style></style>
