<template>
  <v-sheet class="pa-2" border rounded="lg" :height="height">
    <MapboxMap
      ref="mapboxMap"
      :accessToken="accessToken"
      mapStyle="mapbox://styles/mapbox/satellite-streets-v12"
      @mb-created="(mapInstance) => (map = mapInstance)"
      :center="mapCenter"
      :zoom="zoom"
      :style="{ height: '100%' }"
    ></MapboxMap>
  </v-sheet>
</template>

<script>
import { MapboxMap } from "@studiometa/vue-mapbox-gl";

export default {
  name: "PolyMap",
  components: { MapboxMap },
  props: {
    polygon: {
      type: Object,
      default: () => ({
        type: "Polygon",
        coordinates: [[]],
      }),
    },
    zoom: {
      type: Number,
      default: 16,
    },
    height: {
      type: String,
      default: "100%",
    },
    width: {
      type: String,
      default: "100%",
    },
  },
  setup() {
    const accessToken = process.env.VUE_APP_MAPBOX_TOKEN;

    return { accessToken };
  },
  data() {
    return {
      map: null,
    };
  },
  watch: {
    polygon: {
      handler(polygon) {
        if (this.map) {
          this.map.getSource("polygon").setData({
            type: "Feature",
            geometry: polygon,
          });
        }
      },
      deep: true,
    },
  },
  computed: {
    mapCenter() {
      const coordinates = this.polygon.coordinates[0];
      const sum = coordinates.reduce(
        (acc, [lng, lat]) => [acc[0] + lng, acc[1] + lat],
        [0, 0]
      );
      const center = [sum[0] / coordinates.length, sum[1] / coordinates.length];
      return center;
    },
  },
  mounted() {
    this.map.on("load", () => {
      this.map.addSource("polygon", {
        type: "geojson",
        data: {
          type: "Feature",
          geometry: this.polygon,
        },
      });
      this.map.addLayer({
        id: "polygon",
        type: "fill",
        source: "polygon",
        layout: {},
        paint: {
          "fill-color": "#088",
          "fill-opacity": 0.8,
        },
      });
    });
  },
};
</script>

<style></style>
