<template>
  <v-card
    elevation="0"
    border
    rounded
    class="pa-5"
    height="85%"
    min-height="350"
  >
    <v-card-title class="d-flex justify-space-between align-center">
      <span class="text-h4">Períodos associados</span>
      <AddRegionPeriod
        v-if="
          accountStore.hasRole(['Produtor', 'Organização', 'Administrador'])
        "
        :region="region"
        :regioninperiods="periods"
        class="mb-5"
        @refreshRegionPeriods="refreshPeriods()"
      ></AddRegionPeriod>
    </v-card-title>
    <v-card-text class="mt-5">
      <v-data-table-virtual
        fixed-headers
        no-data-text="Sem períodos associados"
        :headers="headers"
        :items="tableItems"
        :loading="isLoading"
      >
        <template v-slot:item="row">
          <tr :class="isCurrent(row) ? 'current' : ''">
            <td>{{ row.item.name }}</td>
            <td>{{ row.item.type }}</td>
            <td>{{ row.item.start_date }}</td>
            <td>{{ row.item.end_date }}</td>
            <td>
              <v-btn
                color="secondary"
                variant="text"
                icon
                @click="openEnemiesDialog(row.item)"
              >
                <v-icon>mdi-bug</v-icon>
                <v-tooltip activator="parent" location="bottom">
                  Inimigos
                </v-tooltip>
              </v-btn>
              <v-btn
                v-if="
                  accountStore.hasRole([
                    'Produtor',
                    'Organização',
                    'Administrador',
                  ])
                "
                color="error"
                variant="text"
                icon
                @click="openDeleteDialog(row.item)"
              >
                <v-icon>mdi-delete-outline</v-icon>
                <v-tooltip activator="parent" location="bottom">
                  Remover
                </v-tooltip>
              </v-btn>
            </td>
          </tr>
        </template>
        <template v-if="hasMore && !isLoading" v-slot:[`body.append`]>
          <tr
            v-intersect.quiet="{
              handler: loadMore,
              options: {
                threshold: 0.5,
              },
            }"
          >
            <td :colspan="headers.length" class="text-center">
              <v-skeleton-loader type="table-row"></v-skeleton-loader>
            </td>
          </tr>
        </template>
      </v-data-table-virtual>
      <v-dialog
        v-model="deleteDialog"
        persistent
        width="auto"
        @afterLeave="resetDeleteDialog"
      >
        <v-card width="40rem">
          <v-card-title class="bg-error d-flex align-center">
            <v-icon size="small" class="mr-2">mdi-delete-outline</v-icon>
            <span>Remover perído da região</span></v-card-title
          >
          <v-card-text>
            <span
              >Tem a certeza que quer remover:
              <b>{{ periodToDelete.period_name }}</b></span
            >
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions class="d-flex justify-space-around">
            <v-btn
              color="error"
              variant="tonal"
              icon="mdi-close"
              @click="deleteDialog = false"
            ></v-btn>
            <v-btn
              color="secondary"
              variant="tonal"
              icon="mdi-check"
              :loading="isLoadingDelete"
              @click="deleteRegionPeriod()"
            ></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <EnemiesinPeriod
        :region="region"
        :regioninperiod="period"
        :dialog="enemiesDialog"
        @update:dialog="enemiesDialog = $event"
      ></EnemiesinPeriod>
    </v-card-text>
  </v-card>
</template>

<script>
import AddRegionPeriod from "./AddRegionPeriod.vue";
import ProtocolsinPeriod from "./ProtocolsinPeriod.vue";
import EnemiesinPeriod from "./EnemiesinPeriod.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "PeriodsDetail",
  components: { AddRegionPeriod, ProtocolsinPeriod, EnemiesinPeriod },
  props: {
    region: Object,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      //list periods
      isLoading: false,
      periods: [],
      limit: 10,
      offset: 0,
      hasMore: false,
      headers: [
        {
          title: "Nome",
          key: "name",
        },
        {
          title: "Tipo",
          key: "type",
        },
        {
          title: "Data de Início",
          key: "start_date",
        },
        {
          title: "Data de Fim",
          key: "end_date",
        },
        {
          title: "",
          key: "actions",
        },
      ],
      //delete period
      deleteDialog: false,
      periodToDelete: {
        regioninperiod_id: 0,
        period_name: "",
      },
      isLoadingDelete: false,

      //enemies
      enemiesDialog: false,
      period: {},
    };
  },
  computed: {
    tableItems() {
      return this.periods.map((regioninperiod) => {
        return {
          regioninperiod_id: regioninperiod.regioninperiod_id,
          name: regioninperiod.period.period_name,
          type: regioninperiod.period.period_type.periodtype_name,
          start_date: this.formatDate(regioninperiod.period.period_startdate),
          end_date: this.formatDate(regioninperiod.period.period_enddate),
        };
      });
    },
  },
  methods: {
    async getRegionPeriods() {
      this.isLoading = true;
      await axios
        .request({
          url: "api/regions/" + this.region.region_id + "/regionsinperiods",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            limit: this.limit,
            offset: this.offset,
          },
        })
        .then((response) => {
          let _periods = response.data.regioninperiods;
          this.periods.push(..._periods);
          this.offset += _periods.length;
          this.hasMore = response.data.hasMore;
          this.isLoading = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoading = false;
        });
    },
    loadMore(isIntersecting) {
      if (isIntersecting) {
        this.getRegionPeriods();
      }
    },
    refreshPeriods() {
      this.periods = [];
      this.offset = 0;
      this.hasMore = false;
      this.$emit("refreshEOIs");
      this.getRegionPeriods();
    },
    deleteRegionPeriod() {
      this.isLoadingDelete = true;
      axios
        .request({
          url: "api/regionsinperiods/" + this.periodToDelete.regioninperiod_id,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.isLoadingDelete = false;
          this.deleteDialog = false;
          this.generalStore.successSnackbar(
            "Período removido com sucesso da região"
          );
          this.refreshPeriods();
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingDelete = false;
          this.generalStore.errorSnackbar(error.response.data.error, 3000);
        });
    },
    formatDate(date) {
      return new Date(date).toLocaleDateString("pt-PT");
    },
    openDeleteDialog(period) {
      this.periodToDelete = {
        regioninperiod_id: period.regioninperiod_id,
        period_name: period.name,
      };
      this.deleteDialog = true;
    },
    resetDeleteDialog() {
      this.periodToDelete = {
        regioninperiod_id: 0,
        period_name: "",
      };
    },

    openEnemiesDialog(period) {
      this.period = period;
      this.enemiesDialog = true;
    },
    isCurrent(row) {
      const currentDate = new Date().getTime();
      const startDate = new Date(row.item.start_date).getTime();
      const endDate = new Date(row.item.end_date).getTime();

      return startDate <= currentDate && currentDate <= endDate;
    },
  },
  mounted() {
    this.getRegionPeriods();
  },
  unmounted() {
    this.periods = [];
  },
};
</script>

<style scoped>
.current {
  background-color: #5cf5a3;
}
</style>
