<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn color="success" append-icon="mdi-plus" v-bind="activatorProps"
        >Nova Parcela</v-btn
      >
    </template>
    <template v-slot:default="{}">
      <v-card class="mx-auto" width="500">
        <v-card-title class="bg-success">{{ steps[step].title }}</v-card-title>
        <v-window v-model="step">
          <!-- <v-window-item v-if="accountStore.hasRole(['Organização'])">
            <v-card-text>
              <v-sheet border rounded class="pa-5">
                <v-form v-model="steps[step].formValid">
                  <span class="text-subtitle-1 text-medium-emphasis">
                    Proprietário(s)*
                  </span>
                  <v-autocomplete
                    v-model="owners"
                    :items="orgMembers"
                    multiple
                    chips
                    item-title="agent_name"
                    item-value="agent_id"
                    placeholder="Escolhe um produtor"
                    color="secondary"
                    density="compact"
                    variant="outlined"
                    :rules="rules.required"
                    :disabled="isLoadingMembers"
                  ></v-autocomplete>
                </v-form>
              </v-sheet>
            </v-card-text>
          </v-window-item> -->
          <v-window-item>
            <v-card-text>
              <v-form v-model="steps[step].formValid">
                <!-- <span class="text-subtitle-1 text-medium-emphasis">
                  Ficheiro GeoJSON*
                </span> -->
                <v-row>
                  <v-col>
                    <v-autocomplete
                      class="mt-4"
                      v-model="region.regiontype_id"
                      :items="regiontypes"
                      item-title="regiontype_name"
                      item-value="regiontype_id"
                      label="Tipo de Região*"
                      prepend-inner-icon="mdi-land-plots-circle-variant"
                      no-data-text="Nenhum tipo de região encontrado"
                      density="compact"
                      variant="outlined"
                      :rules="[rules.required]"
                      :loading="isLoadingRegionTypes"
                    ></v-autocomplete>
                  </v-col>
                  <v-col>
                    <v-file-input
                      v-model="file"
                      accept=".json"
                      class="mt-4"
                      label="Ficheiro GeoJSON"
                      density="compact"
                      variant="outlined"
                      chips
                      clearable
                      prepend-icon=""
                      prepend-inner-icon="mdi-file-outline"
                      :rules="[rules.requiredfile]"
                      @update:model-value="readFile"
                      @click:clear="region.geometry = null"
                    ></v-file-input
                  ></v-col>
                </v-row>
              </v-form>
              <div v-if="region?.geometry != null">
                <PolyMap
                  :polygon="region.geometry"
                  :zoom="15"
                  height="300px"
                ></PolyMap>
              </div>
            </v-card-text>
          </v-window-item>
          <v-window-item>
            <v-card-text>
              <v-form v-model="steps[step].formValid" ref="form1">
                <v-row>
                  <v-col cols="8">
                    <v-text-field
                      v-model.trim="region.region_name"
                      label="Nome*"
                      prepend-inner-icon="mdi-tag-text-outline"
                      density="compact"
                      variant="outlined"
                      :rules="[rules.required, rules.maxLength]"
                    ></v-text-field
                  ></v-col>
                  <v-col cols="4">
                    <v-text-field
                      v-model.trim="region.region_code"
                      label="Código*"
                      prepend-inner-icon="mdi-tag-outline"
                      density="compact"
                      variant="outlined"
                      :rules="[rules.required, rules.code]"
                    ></v-text-field
                  ></v-col>
                  <v-col cols="6">
                    <v-autocomplete
                      v-model="region.crop"
                      :items="crops"
                      item-title="crop_name"
                      return-object
                      label="Cultura*"
                      prepend-inner-icon="mdi-tree-outline"
                      no-data-text="Nenhuma cultura encontrada"
                      density="compact"
                      variant="outlined"
                      :rules="[rules.required]"
                      :loading="isLoadingCrops"
                      @update:modelValue="refreshVarieties()"
                    ></v-autocomplete
                  ></v-col>
                  <v-col cols="6">
                    <v-autocomplete
                      v-model="region.variety"
                      :items="varieties"
                      item-title="variety_name"
                      return-object
                      label="Variedade*"
                      prepend-inner-icon="mdi-food-apple-outline"
                      no-data-text="Nenhuma variedade encontrada"
                      density="compact"
                      variant="outlined"
                      :rules="[rules.required]"
                      :disabled="!region.crop"
                      :loading="isLoadingVarieties"
                    ></v-autocomplete>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      v-model.trim="region.local"
                      label="Localidade*"
                      prepend-inner-icon="mdi-map-marker-outline"
                      density="compact"
                      variant="outlined"
                    ></v-text-field
                  ></v-col>
                  <v-col cols="12">
                    <v-textarea
                      v-model.trim="region.region_description"
                      label="Descrição"
                      prepend-inner-icon="mdi-text-box-outline"
                      density="compact"
                      variant="outlined"
                      :rules="[rules.description]"
                    ></v-textarea>
                  </v-col>
                </v-row>
              </v-form>
            </v-card-text>
          </v-window-item>
          <v-window-item>
            <v-card-text>
              <v-form v-model="steps[step].formValid">
                <v-row>
                  <v-col cols="12">
                    <v-autocomplete
                      v-model="region.periods"
                      class="mt-4"
                      :items="periods"
                      label="Período(s)*"
                      placeholder="Selecione um período"
                      prepend-inner-icon="mdi-calendar"
                      no-data-text="Nenhum período encontrado"
                      item-title="period_name"
                      item-value="period_id"
                      density="compact"
                      variant="outlined"
                      clearable
                      multiple
                      chips
                      :loading="isLoadingPeriods"
                      :rules="[rules.requiredArray]"
                    >
                      <template v-slot:item="{ props, item }">
                        <v-list-item
                          v-bind="props"
                          :subtitle="
                            formatDate(item.raw.period_startdate) +
                            ' - ' +
                            formatDate(item.raw.period_enddate)
                          "
                        ></v-list-item>
                      </template>
                    </v-autocomplete>
                  </v-col>
                  <v-col cols="12">
                    <v-autocomplete
                      v-model="region.enemies"
                      class="mt-4"
                      :items="enemiesSelectItems"
                      label="Inimigo(s)*"
                      placeholder="Selecione um inimigo"
                      prepend-inner-icon="mdi-bug-outline"
                      no-data-text="Nenhum inimigo encontrado"
                      item-title="enemy_name"
                      item-value="enemy_id"
                      density="compact"
                      variant="outlined"
                      clearable
                      multiple
                      chips
                      :loading="isLoadingEnemies"
                      :rules="[rules.requiredArray]"
                    ></v-autocomplete>
                  </v-col>
                </v-row>
              </v-form>
            </v-card-text>
          </v-window-item>
        </v-window>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn
            v-if="step > 0"
            prepend-icon="mdi-chevron-left"
            variant="tonal"
            @click="step--"
          >
            Anterior
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            v-if="step < lasStep"
            :disabled="!steps[step].formValid"
            append-icon="mdi-chevron-right"
            color="secondary"
            variant="tonal"
            @click="step++"
          >
            Próximo
          </v-btn>
          <v-btn
            v-else-if="step === lasStep"
            :disabled="!steps[lasStep].formValid"
            color="success"
            variant="flat"
            :loading="isLoadingPost"
            @click="postRegion()"
          >
            Adicionar
          </v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import PolyMap from "@/components/MapUtils/PolyMap.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "AddRegionOrg",
  components: { PolyMap },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      owners: null,
      region: {
        region_code: null,
        region_name: null,
        crop: null,
        variety: null,
        region_description: null,
        local: null,
        geometry: null,
        periods: [],
        enemies: [],
      },
      file: null,
      dialog: false,
      step: 0,
      steps: [
        {
          title: "Região",
          formValid: false,
        },
        {
          title: "Detalhes",
          formValid: false,
        },
        {
          title: "Períodos e Inimigos",
          formValid: false,
        },
      ],
      rules: {
        required: (v) => !!v || "Campo obrigatório.",
        requiredArray: (v) => v.length > 0 || "Campo obrigatório.",
        code: (v) =>
          (v && v.length <= 3 && v.toUpperCase() === v) || "Código inválido",
        maxLength: (v) => (v && v.length <= 25) || "Máximo de 25 caracteres.",
        description: (v) =>
          (v && v.length <= 255) || !v || "Máximo de 255 caracteres.",
        requiredfile: (v) => {
          if (v.length == 0) {
            return "Selecione um ficheiro JSON.";
          } else {
            return true;
          }
        },
      },
      isLoadingPost: false,

      regiontypes: [],
      isLoadingRegionTypes: false,

      orgMembers: [],
      isLoadingMembers: false,

      crops: [],
      isLoadingCrops: false,

      varieties: [],
      isLoadingVarieties: false,

      períods: [],
      isLoadingPeríods: false,

      enemies: [],
      isLoadingEnemies: false,
    };
  },
  watch: {
    dialog(val) {
      if (val) {
        this.onOpendDialog();
      }
    },
  },
  computed: {
    lasStep() {
      return this.steps.length - 1;
    },

    enemiesSelectItems() {
      return this.enemies.filter((enemy) => enemy.enemytype_id != 1); //HARD CODED 1 = Not applicable
    },
  },
  methods: {
    onOpendDialog() {
      // if (this.accountStore.hasRole(["Organização"])) {
      //   this.steps.unshift({
      //     title: "Proprietário(s)",
      //     formValid: false,
      //   });
      //   this.getOrgMembers();
      // }
      this.getRegionTypes();
      this.getCrops();
      this.getPeriods();
      this.getEnemies();
    },
    getRegionTypes() {
      this.isLoadingRegionTypes = true;
      axios
        .request({
          url: "api/regiontypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.regiontypes = response.data;
          this.isLoadingRegionTypes = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingRegionTypes = false;
          this.generalStore.errorSnackbar("Erro ao obter tipos de região.");
        });
    },
    getOrgMembers() {
      this.isLoadingMembers = true;
      axios
        .request({
          url:
            "api/organizations/" +
            this.accountStore.account.organization +
            "/organizationproducers",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.orgMembers = response.data;
          this.isLoadingMembers = false;
        })
        .catch((error) => {
          this.isLoadingMembers = false;
          this.generalStore.errorSnackbar("Erro ao obter produtores.");
        });
    },
    async getCrops() {
      this.isLoadingCrops = true;
      await axios
        .request({
          url: "api/crops",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.crops = response.data;
          this.isLoadingCrops = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingCrops = false;
          this.generalStore.errorSnackbar("Erro ao obter culturas.");
        });
    },
    refreshVarieties() {
      this.region.variety = null;
      this.$refs.form1.validate();
      this.getVarieties();
    },
    async getVarieties() {
      this.isLoadingVarieties = true;
      await axios
        .request({
          url: "api/crops/" + this.region.crop.crop_id + "/varieties",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.varieties = response.data;
          this.isLoadingVarieties = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingVarieties = false;
          this.generalStore.errorSnackbar("Erro ao obter variedades.");
        });
    },
    async getPeriods() {
      this.isLoadingPeriods = true;
      await axios
        .request({
          url: "api/periods",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.periods = response.data.periods;
          this.isLoadingPeriods = false;
          this.region.periods.push(
            this.getRecentPeriod(this.periods).period_id
          );
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao buscar períodos");
          this.isLoadingPeriods = false;
        });
    },
    getRecentPeriod(periods) {
      const recentPeriod = periods.reduce((acc, curr) => {
        if (new Date(curr.period_enddate) > new Date(acc.period_enddate)) {
          return curr;
        }
        return acc;
      }, periods[0]);
      return recentPeriod;
    },

    formatDate(date) {
      return new Date(date).toLocaleDateString("pt-PT");
    },

    async getEnemies() {
      this.isLoadingEnemies = true;
      await axios
        .request({
          url: "api/enemies",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.enemies = response.data;
          this.isLoadingEnemies = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao buscar inimigos");
          this.isLoadingEnemies = false;
        });
    },

    async postRegion() {
      this.isLoadingPost = true;
      let payload = {
        regiontype_id: this.region.regiontype_id,
        // organization_id: this.accountStore.account.organization,
        owners: this.owners,
        region_code: this.region.region_code,
        region_name: this.region.region_name,
        crop: { ...this.region.crop, variety: this.region.variety },
        region_description: this.region.region_description,
        local: this.region.local,
        geometry: this.region.geometry,
        regioninperiods: {
          periods: this.region.periods,
          enemies: this.region.enemies,
        },
      };
      console.log(payload);
      await axios
        .request({
          url: "api/regions",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingPost = false;
          this.dialog = false;
          this.generalStore.successSnackbar("Parcela adicionada com sucesso.");
          this.$emit("refreshRegions");
        })
        .catch((error) => {
          this.isLoadingPost = false;
          console.log(error);
          this.generalStore.errorSnackbar(error.response.data.error);
        });
    },
    async getNearestLocality(geometry) {
      const center = this.getCenter(geometry);
      console.log(center);
      return await axios
        .request({
          url: "https://api.mapbox.com/search/geocode/v6/reverse",
          method: "GET",
          params: {
            longitude: center[0],
            latitude: center[1],
            access_token:
              "pk.eyJ1IjoibXJiYWlhbyIsImEiOiJjbHYwczh3enUxajNvMmlyejE3YmxoaHN4In0.1BrZQAvQO5XSHkxl7HgINg",
          },
        })
        .then((response) => {
          return response.data.features[0].properties.context.region.name;
        })
        .catch((error) => {
          console.log(error);
          return null;
        });
    },
    getCenter(polygon) {
      const coordinates = polygon.coordinates[0];
      const sum = coordinates.reduce(
        (acc, [lng, lat]) => [acc[0] + lng, acc[1] + lat],
        [0, 0]
      );
      const center = [sum[0] / coordinates.length, sum[1] / coordinates.length];
      return center;
    },
    readFile(event) {
      if (!event) return;
      const reader = new FileReader();
      reader.onload = async (e) => {
        try {
          let obj = JSON.parse(e.target.result);
          console.log(obj);
          const _region = obj.features.find(
            (f) => f.geometry.type === "Polygon"
          );
          console.log(_region);
          this.region.region_code = _region.properties?.code;
          this.region.region_name = _region.properties?.name;
          this.region.region_description = _region.properties?.description;
          this.region.geometry = _region.geometry;
          this.region.local = await this.getNearestLocality(_region.geometry);
        } catch (error) {
          console.log(error);
          this.file = null;
          this.generalStore.errorSnackbar("Erro ao ler ficheiro GeoJSON.");
        }
      };
      reader.readAsText(event);
    },
    reset() {
      this.owner_id = null;
      this.file = null;
      this.region = {
        region_code: null,
        region_name: null,
        crop: null,
        variety: null,
        region_description: null,
        local: null,
        geometry: null,
        periods: [],
        enemies: [],
      };
      this.step = 0;
    },
  },
};
</script>

<style></style>
