<template>
  <v-card
    elevation="0"
    border
    rounded
    class="pa-5"
    height="85%"
    min-height="350"
  >
    <v-card-title class="d-flex justify-space-between align-center">
      <div>
        <span class="text-h4">Estações Meterológicas</span>
        <v-text-field
          v-model="filter.datasource_name"
          color="secondary"
          placeholder="Pesquisar"
          density="compact"
          variant="solo"
          hide-details
          append-icon="mdi-magnify"
          class="mt-5"
          clearable
          @click:append="refreshDatasources()"
          @click:clear="refreshDatasources()"
        ></v-text-field>
      </div>
    </v-card-title>
    <v-card-text class="scrollable-card mt-5">
      <v-progress-linear
        v-if="isLoadingList"
        color="primary"
        indeterminate
      ></v-progress-linear>
      <v-data-iterator
        v-else
        :items="datasources"
        item-value="regiondatasource_id"
        :items-per-page="datasources.length + 1"
      >
        <template v-slot:default="{ items, isExpanded, toggleExpand }">
          <v-row>
            <v-col
              cols="12"
              v-for="item in items"
              :key="item.raw.regiondatasource_id"
            >
              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ item.raw.datasource_name }}</span>
                </v-card-title>
                <div class="px-4">
                  <v-switch
                    :label="`${
                      isExpanded(item) ? 'Esconder' : 'Mostrar'
                    } Detalhes`"
                    :model-value="isExpanded(item)"
                    density="compact"
                    color="secondary"
                    @click="() => toggleExpand(item)"
                  ></v-switch>
                  <v-expand-transition>
                    <div v-if="isExpanded(item)" class="mb-5">
                      <DatasourceDetail
                        :datasource_id="item.raw.datasource_id"
                      ></DatasourceDetail>
                    </div>
                  </v-expand-transition>
                </div>
              </v-card>
            </v-col>
            <v-col cols="12" v-if="hasMore">
              <v-skeleton-loader
                v-intersect.quiet="{
                  handler: loadMore,
                  options: {
                    threshold: 0.3,
                  },
                }"
                type="card"
              ></v-skeleton-loader>
            </v-col>
            <v-col v-else cols="12">
              <br />
            </v-col>
          </v-row>
        </template>
      </v-data-iterator>
    </v-card-text>
  </v-card>
</template>

<script>
import DatasourceDetail from "./DatasourceDetail.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "DatasourcesDetail",
  components: { DatasourceDetail },
  props: {
    region: Object,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      datasources: [],
      isLoadingList: false,
      limit: 5,
      offset: 0,
      hasMore: false,

      filter: {
        datasource_name: null,
      },
    };
  },
  computed: {},
  methods: {
    async getDatasources() {
      await axios
        .request({
          url: "api/regions/" + this.region.region_id + "/regionsdatasources",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            limit: this.limit,
            offset: this.offset,

            //filter search
            datasource_name: this.filter.datasource_name,
          },
        })
        .then((response) => {
          let _datasources = response.data.datasources;
          this.datasources.push(..._datasources);
          this.offset += _datasources.length;
          this.hasMore = response.data.hasMore;
        })
        .catch((error) => {
          console.error(error);
        });
    },
    refreshDatasources() {
      this.datasources = [];
      this.offset = 0;
      this.hasMore = false;
      this.getDatasources();
    },
    loadMore(isIntersecting) {
      if (isIntersecting) {
        this.getDatasources();
      }
    },
  },
  mounted() {
    this.isLoadingList = true;
    this.getDatasources().then(() => {
      this.isLoadingList = false;
    });
  },
  unmounted() {
    this.datasources = [];
  },
};
</script>

<style scoped>
.scrollable-card {
  max-height: 90%;
  overflow: auto;
}
</style>
