<template>
  <v-card
    elevation="0"
    border
    rounded
    class="pa-5"
    height="85%"
    min-height="350"
  >
    <v-card-title class="d-flex justify-space-between align-center">
      <span class="text-h4">Utilizadores Associados</span>
      <AddRegionAgent
        v-if="this.accountStore.hasRole('Organização')"
        :region="region"
        :existingAgents="tableItems"
        class="mb-5"
        @refresh="refreshAgents"
      ></AddRegionAgent>
    </v-card-title>
    <v-card-text class="mt-5">
      <v-data-table-virtual
        fixed-headers
        no-data-text="Sem utilizadores associados"
        :headers="headers"
        :items="tableItems"
        :loading="isLoadingList"
      >
        <template v-slot:item="row">
          <tr>
            <td>
              <v-chip
                :color="roleColor(row.item.agent.type.name)"
                variant="flat"
                >{{ row.item.role }}</v-chip
              >
            </td>
            <td>{{ row.item.agent.username }}</td>
            <td>{{ row.item.agent.name }}</td>
            <td>{{ row.item.startDate }}</td>
            <td v-if="this.accountStore.hasRole('Organização')">
              <v-btn
                color="warning"
                variant="text"
                icon
                @click="openEditDialog(row.item)"
              >
                <v-icon>mdi-pencil</v-icon>
                <v-tooltip activator="parent" location="bottom"
                  >Editar data de início</v-tooltip
                >
              </v-btn>
              <v-btn
                color="error"
                variant="text"
                icon
                @click="openDeleteDialog(row.item)"
              >
                <v-icon>mdi-delete-outline</v-icon>
                <v-tooltip activator="parent" location="bottom">
                  Remover
                </v-tooltip>
              </v-btn>
            </td>
          </tr>
        </template>
      </v-data-table-virtual>
      <v-dialog v-model="editDialog" @afterLeave="resetEditDialog" width="auto">
        <v-card width="400">
          <v-card-title class="bg-warning d-flex align-center">
            <v-icon size="small" class="mr-2">mdi-pencil</v-icon>
            <span
              >Editar data de início de
              <span class="font-weight-light">{{
                agentToEdit?.agent?.name
              }}</span></span
            >
          </v-card-title>
          <v-card-text>
            <v-form v-model="editFormValid">
              <div class="mt-4">
                <v-locale-provider locale="pt">
                  <v-date-input
                    v-model="startDate"
                    label="Data de início"
                    placeholder="dd/mm/aaaa"
                    prepend-icon=""
                    prepend-inner-icon="mdi-calendar-start"
                    density="compact"
                    variant="outlined"
                    :rules="[(v) => !!v || 'Campo obrigatório']"
                  ></v-date-input>
                </v-locale-provider>
              </div>
            </v-form>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions class="d-flex justify-space-around">
            <v-btn
              color="error"
              variant="tonal"
              icon="mdi-close"
              @click="editDialog = false"
            ></v-btn>
            <v-btn
              color="secondary"
              variant="tonal"
              icon="mdi-check"
              :loading="isLoadingEdit"
              :disabled="!editFormValid"
              @click="editAgent()"
            ></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
      <v-dialog
        v-model="deleteDialog"
        @afterLeave="resetDeleteDialog"
        width="auto"
      >
        <v-card>
          <v-card-title class="bg-error d-flex align-center">
            <v-icon size="small" class="mr-2">mdi-delete-outline</v-icon>
            <span
              >Desassociar
              <span class="font-weight-light">{{
                agentToDelete?.agent?.type?.name
              }}</span></span
            ></v-card-title
          >
          <v-card-text>
            <span
              >Tem a certeza que quer remover o
              <strong
                >{{ agentToDelete?.role }}
                {{ agentToDelete?.agent?.name }} </strong
              >?</span
            >
            <v-form v-model="deleteFormValid">
              <div class="mt-4">
                <v-locale-provider locale="pt">
                  <v-date-input
                    v-model="endDate"
                    label=" Ultima data de atuação"
                    placeholder="dd/mm/aaaa"
                    prepend-icon=""
                    prepend-inner-icon="mdi-calendar"
                    density="compact"
                    variant="outlined"
                    :rules="[(v) => !!v || 'Campo obrigatório']"
                  ></v-date-input>
                </v-locale-provider>
              </div>
            </v-form>
          </v-card-text>
          <v-card-actions class="d-flex justify-space-around">
            <v-btn
              color="error"
              variant="tonal"
              icon="mdi-close"
              @click="deleteDialog = false"
            ></v-btn>
            <v-btn
              color="secondary"
              variant="tonal"
              icon="mdi-check"
              :loading="isLoadingDelete"
              :disabled="!deleteFormValid"
              @click="deleteAgent()"
            ></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-card-text>
  </v-card>
</template>

<script>
import AddRegionAgent from "./AddRegionAgent.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";
export default {
  name: "RegionAgentsDetail",
  components: {
    AddRegionAgent,
  },
  props: {
    region: Object,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      agentsregions: [],
      isLoadingList: false,
      limit: 10,
      offset: 0,
      hasMore: false,

      //edit agent
      editDialog: false,
      agentToEdit: {
        agent_id: 0,
        agent_name: "",
      },
      editFormValid: false,
      startDate: new Date(),
      isLoadingEdit: false,

      //delete agent
      deleteDialog: false,
      agentToDelete: {
        agent_id: 0,
        agent_name: "",
      },
      deleteFormValid: false,
      endDate: new Date(),
      isLoadingDelete: false,
    };
  },
  computed: {
    headers() {
      let headers = [
        {
          title: "Tipo",
          key: "type",
        },
        {
          title: "Conta",
          key: "username",
        },
        {
          title: "Nome",
          key: "name",
        },
        {
          title: "Data de Início",
          key: "startDate",
        },
      ];

      if (this.accountStore.hasRole("Organização")) {
        headers.push({
          title: "",
          key: "actions",
        });
      }
      return headers;
    },
    tableItems() {
      return this.agentsregions.map((agentregion) => {
        return {
          agentregion_id: agentregion.id,
          agent: agentregion.agent,
          role:
            agentregion.agent.type.name == "Produtor"
              ? "Proprietário"
              : agentregion.agent.type.name,
          startDate: agentregion.metadata?.startDate,
        };
      });
    },

    startDateFormatted() {
      return this.startDate.toLocaleDateString("pt-PT");
    },
    endDateFormatted() {
      return this.endDate.toLocaleDateString("pt-PT");
    },
  },
  methods: {
    async listAgents() {
      this.isLoadingList = true;
      await axios
        .request({
          url: "api/agentsregions",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            region_id: this.region.region_id,
            limit: this.limit,
            offset: this.offset,
          },
        })
        .then((response) => {
          let _agents = response.data.agentsregions;
          this.agentsregions.push(..._agents);
          this.offset += _agents.length;
          this.hasMore = response.data.hasMore;
          this.isLoadingList = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingList = false;
        });
    },
    loadMore(isIntersecting) {
      if (isIntersecting) {
        this.listAgents();
      }
    },
    refreshAgents() {
      this.offset = 0;
      this.agentsregions = [];
      this.listAgents();
    },
    roleColor(role) {
      switch (role) {
        case "Administrador":
          return "error";
        case "Organização":
          return "warning";
        case "Produtor":
          return "success";
        case "Agente de Campo":
          return "info";
        default:
          return "primary";
      }
    },
    openEditDialog(agent) {
      console.log(agent);
      this.agentToEdit = agent;
      this.startDate = new Date(agent.startDate);
      this.editDialog = true;
    },
    resetEditDialog() {
      this.agentToEdit = {
        agent_id: 0,
        agent_name: "",
      };
      this.startDate = new Date();
      this.editDialog = false;
    },

    async editAgent() {
      this.isLoadingEdit = true;
      await axios
        .request({
          url: "api/agentsregions/" + this.agentToEdit.agentregion_id,
          method: "PATCH",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: {
            startDate: this.startDateFormatted,
          },
        })
        .then((response) => {
          this.isLoadingEdit = false;
          this.generalStore.successSnackbar("Data de início atualizada.");
          this.refreshAgents();
          this.resetEditDialog();
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao atualizar data de início.");
          this.isLoadingEdit = false;
        });
    },
    openDeleteDialog(agent) {
      console.log(agent);
      this.agentToDelete = agent;
      this.deleteDialog = true;
    },
    resetDeleteDialog() {
      this.agentToDelete = {
        agent_id: 0,
        agent_name: "",
      };
      this.endDate = new Date();
      this.deleteDialog = false;
    },
    async deleteAgent() {
      this.isLoadingDelete = true;
      await axios
        .request({
          url: "api/agentsregions/" + this.agentToDelete.agentregion_id,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: {
            endDate: this.endDateFormatted,
          },
        })
        .then((response) => {
          this.isLoadingDelete = false;
          this.generalStore.successSnackbar("Agente removido com sucesso.");
          this.refreshAgents();
          this.resetDeleteDialog();
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao remover agente.");
          this.isLoadingDelete = false;
        });
    },
  },
  mounted() {
    this.listAgents();
  },
  unmounted() {
    this.agentsregions = [];
  },
};
</script>

<style></style>
