<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn color="success" icon="mdi-plus" v-bind="activatorProps"> </v-btn>
    </template>
    <template v-slot:default="{}">
      <v-card class="mx-auto" min-width="500">
        <v-card-title class="bg-success d-flex align-center">
          {{ steps[step].title }}
        </v-card-title>
        <v-window v-model="step">
          <v-window-item :value="0">
            <v-card-text>
              <v-row justify="center">
                <v-col cols="10">
                  <v-form v-model="steps[step].formValid">
                    <!-- <span class="text-subtitle-1 text-medium-emphasis">
                    Período da região*
                  </span> -->
                    <v-select
                      v-model="eoi.regioninperiod_id"
                      class="mt-4"
                      :items="regionPeriods"
                      label="Período"
                      placeholder="Selecione um período"
                      prepend-inner-icon="mdi-calendar"
                      no-data-text="Nenhum período encontrado"
                      item-title="period.period_name"
                      item-value="regioninperiod_id"
                      density="compact"
                      variant="outlined"
                      :disabled="isLoadingPeriods"
                    >
                      <template v-slot:item="{ props, item }">
                        <v-list-item
                          v-bind="props"
                          :subtitle="
                            item.raw.period.period_name != 'Permanente'
                              ? formatDate(item.raw.period.period_startdate) +
                                ' - ' +
                                formatDate(item.raw.period.period_enddate)
                              : ''
                          "
                        ></v-list-item>
                      </template>
                    </v-select>
                  </v-form>
                </v-col>
              </v-row>
            </v-card-text>
          </v-window-item>
          <v-window-item :value="1">
            <v-card-text>
              <v-form v-model="steps[step].formValid">
                <v-row>
                  <v-col cols="6">
                    <v-select
                      v-model="eoi.eoitype_id"
                      class="mt-4"
                      :items="eoiTypes"
                      label="Tipo de EOI*"
                      placeholder="Selecione um tipo"
                      prepend-inner-icon="mdi-map-marker-alert-outline"
                      no-data-text="Nenhum tipo de EOI encontrado"
                      item-title="eoitype_name"
                      item-value="eoitype_id"
                      density="compact"
                      variant="outlined"
                      :disabled="isLoadingTypes"
                      :rules="[rules.required]"
                    ></v-select
                  ></v-col>
                  <v-col cols="6">
                    <v-file-input
                      accept=".json"
                      class="mt-4"
                      label="Ficheiro GeoJSON*"
                      placeholder="Selecione um ficheiro GeoJSON"
                      density="compact"
                      variant="outlined"
                      chips
                      clearable
                      prepend-icon=""
                      prepend-inner-icon="mdi-file-outline"
                      :rules="[rules.requiredfile]"
                      @update:model-value="readFile"
                      @click:clear="eoi.eoi_geometry = null"
                    ></v-file-input
                  ></v-col>
                  <v-col cols="12">
                    <div v-if="eoi.eoi_geometry">
                      <PolyMap
                        v-if="eoi.eoi_geometry.type == 'Polygon'"
                        :polygon="eoi.eoi_geometry"
                        :zoom="17"
                        height="300px"
                      ></PolyMap>
                      <LineMap
                        v-else-if="eoi.eoi_geometry.type == 'LineString'"
                        :line="eoi.eoi_geometry"
                        :zoom="17"
                        height="300px"
                      ></LineMap>
                      <PointMap
                        v-else-if="eoi.eoi_geometry.type == 'Point'"
                        :point="eoi.eoi_geometry"
                        :zoom="17"
                        height="300px"
                      ></PointMap></div
                  ></v-col>
                </v-row>
              </v-form>
            </v-card-text>
          </v-window-item>
          <v-window-item :value="2">
            <v-card-text>
              <v-form v-model="steps[2].formValid">
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      v-model.trim="eoi.eoi_name"
                      label="Nome do EOI*"
                      placeholder="Insira um nome"
                      prepend-inner-icon="mdi-tag-text-outline"
                      density="compact"
                      variant="outlined"
                      :rules="[rules.required, rules.maxLength]"
                    ></v-text-field
                  ></v-col>
                  <v-col cols="12">
                    <v-textarea
                      v-model.trim="eoi.eoi_description"
                      label="Descrição"
                      placeholder="Insira a descrição"
                      prepend-inner-icon="mdi-text-box-outline"
                      density="compact"
                      variant="outlined"
                      :rules="[rules.description]"
                    ></v-textarea
                  ></v-col>
                </v-row>
              </v-form>
            </v-card-text>
          </v-window-item>
        </v-window>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn
            v-if="step > 0"
            prepend-icon="mdi-chevron-left"
            color="info"
            variant="tonal"
            @click="step--"
          >
            Anterior
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            v-if="step < 2"
            :disabled="!steps[step].formValid"
            append-icon="mdi-chevron-right"
            color="info"
            variant="tonal"
            @click="step++"
          >
            Próximo
          </v-btn>
          <v-btn
            v-else-if="step === 2"
            :disabled="!steps[step].formValid"
            append-icon="mdi-check"
            color="success"
            variant="flat"
            :loading="isLoadingPost"
            @click="postEoi()"
          >
            Adicionar
          </v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import PolyMap from "@/components/MapUtils/PolyMap.vue";
import LineMap from "@/components/MapUtils/LineMap.vue";
import PointMap from "@/components/MapUtils/PointMap.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "AddEoi",
  components: {
    PolyMap,
    LineMap,
    PointMap,
  },
  props: {
    region: {
      type: Object,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      dialog: false,
      step: 0,
      steps: [
        { title: "Período do EOI", formValid: false },
        { title: "Tipo e Localização do EOI", formValid: false },
        { title: "Dados adicionais do EOI", formValid: false },
      ],
      eoiTypes: [],
      regionPeriods: [
        {
          regioninperiod_id: null,
          period: {
            period_id: null,
            period_name: "Permanente",
            period_startdate: null,
            period_enddate: null,
          },
        },
      ],
      eoi: {
        eoitype_id: null,
        region_id: this.region.region_id,
        regioninperiod_id: null,
        eoi_name: null,
        eoi_description: null,
        eoi_geometry: null,
      },
      file: null,
      rules: {
        required: (v) => !!v || "Campo obrigatório",
        maxLength: (v) => (v && v.length <= 25) || "Máximo de 25 caracteres.",
        description: (v) =>
          (v && v.length <= 255) || !v || "Máximo de 255 caracteres.",
        requiredfile: (v) => {
          if (v.length == 0) {
            return "Must select at least one file.";
          } else {
            return true;
          }
        },
      },
      isLoadingTypes: false,
      isLoadingPeriods: false,
      isLoadingPost: false,
    };
  },
  watch: {
    dialog(val) {
      if (val) {
        this.onOpenDialog();
      }
    },
    file(val) {
      if (val) {
        console.log(val);
      }
    },
  },
  computed: {},
  methods: {
    onOpenDialog() {
      this.getEoiTypes();
      this.getRegionPeriods();
    },
    async postEoi() {
      this.isLoadingPost = true;
      console.log(this.eoi);
      await axios({
        url: "api/regions/" + this.region.region_id + "/eois",
        method: "POST",
        headers: {
          Authorization: "Bearer " + this.accountStore.account.token,
        },
        data: this.eoi,
      })
        .then((response) => {
          this.isLoadingPost = false;
          this.generalStore.successSnackbar("EOI adicionada com sucesso");
          this.dialog = false;
          this.$emit("refreshEois");
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingPost = false;
          this.generalStore.errorSnackbar("Erro ao adicionar EOI");
        });
    },
    async getEoiTypes() {
      this.isLoadingTypes = true;
      await axios
        .request({
          url: "api/eoitypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.eoiTypes = response.data;
          this.isLoadingTypes = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao buscar tipos de EOI");
        });
    },
    async getRegionPeriods() {
      this.isLoadingPeriods = true;
      await axios
        .request({
          url: "api/regions/" + this.region.region_id + "/regionsinperiods",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.regionPeriods.push(...response.data.regioninperiods);
          this.isLoadingPeriods = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao buscar períodos da região");
        });
    },
    formatDate(date) {
      return new Date(date).toLocaleDateString("pt-PT");
    },
    reset() {
      this.eoi = {
        eoi_type_id: null,
        region_id: this.region.region_id,
        regioninperiod_id: null,
        eoi_name: null,
        eoi_description: null,
        eoi_geometry: null,
      };
      this.file = null;
      this.step = 0;
      this.eoiTypes = [];
      this.regionPeriods = [
        {
          regioninperiod_id: null,
          period: {
            period_id: null,
            period_name: "Permanente",
            period_startdate: null,
            period_enddate: null,
          },
        },
      ];
    },
    readFile(event) {
      if (!event) return;
      const reader = new FileReader();
      reader.onload = (e) => {
        let obj = JSON.parse(e.target.result);
        const _eoi = obj.features[0];
        this.eoi.eoi_geometry = _eoi.geometry;
        this.eoi.eoi_name = _eoi.properties?.name;
        this.eoi.eoi_description = _eoi.properties?.description;
      };
      reader.readAsText(event);
    },
  },
};
</script>

<style></style>
