<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn
        v-if="editMode"
        color="warning"
        size="small"
        icon
        variant="text"
        v-bind="activatorProps"
      >
        <v-icon>mdi-pencil</v-icon>
        <v-tooltip activator="parent" location="bottom">
          Alterar dados a recolher
        </v-tooltip>
      </v-btn>
      <v-btn
        v-else
        color="success"
        variant="text"
        icon
        v-bind="activatorProps"
      >
        <v-icon>mdi-plus-circle-outline</v-icon>
        <v-tooltip activator="parent" location="bottom">
          Criar estação virtual
        </v-tooltip>
      </v-btn>
    </template>
    <template v-slot:default="{}">
      <v-card>
        <v-card-title
          class="bg-success pl-2"
          :class="{ 'bg-warning': editMode }"
        >
          <div v-if="!editMode" class="d-flex align-center">
            <v-icon size="small" class="mr-2">mdi-plus-circle-outline</v-icon>
            <span>Criar Estação Virtual</span>
          </div>
          <div v-else class="d-flex align-center">
            <v-icon size="small" class="mr-2">mdi-pencil</v-icon>
            <span>Alterar dados a recolher</span>
          </div>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="scrollable-card">
          <div v-if="isLoading">
            <v-skeleton-loader
              v-for="n in 5"
              :key="n"
              type="heading"
            ></v-skeleton-loader>
          </div>
          <div v-else>
            <v-btn class="mr-2" color="info" variant="text" icon @click="selectAll()">
              <v-icon>mdi-playlist-check</v-icon> </v-btn
            ><span>Selecionar todos</span>
            <v-switch
              v-for="(item, index) in seriesVariables"
              :key="index"
              v-model="item.active"
              :label="item.seriesvariable_longname"
              color="secondary"
              density="comfortable"
              hide-details
            ></v-switch></div
        ></v-card-text>
        <v-divider></v-divider>
        <v-card-actions v-if="editMode" class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="dialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingEdit"
            :disabled="!activeSeriesVariables.length"
            @click="editVirtualStation()"
          ></v-btn>
        </v-card-actions>
        <v-card-actions v-else class="d-flex justify-space-between">
          <v-btn color="error" variant="tonal" @click="dialog = false">
            Cancelar
          </v-btn>
          <v-btn
            color="info"
            variant="tonal"
            :loading="isLoadingPost"
            :disabled="!activeSeriesVariables.length"
            @click="postVirtualStation()"
          >
            Criar</v-btn
          >
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "VirtualStationDialog",
  props: {
    region: {
      type: Object,
    },
    editMode: {
      type: Boolean,
      default: false,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      dialog: false,
      seriesVariables: [],
      active: [],
      isLoading: false,

      isLoadingPost: false,

      isLoadingEdit: false,
    };
  },
  watch: {
    async dialog(val) {
      if (val) {
        this.onOpenDialog();
      }
    },
    active(val) {
      console.log(val);
    },
  },
  computed: {
    activeSeriesVariables() {
      return this.seriesVariables.filter((variable) => variable.active);
    },
  },
  methods: {
    async onOpenDialog() {
      await this.getSeriesVariables();
      if (this.editMode) {
        let active = this.region.virtual_datasource.series.map(
          (serie) => serie.seriesvariable_id
        );
        this.seriesVariables = this.seriesVariables.map((variable) => {
          if (active.includes(variable.seriesvariable_id)) {
            return { ...variable, active: true };
          } else {
            return { ...variable, active: false };
          }
        });
      } else {
        this.seriesVariables = this.seriesVariables.map((variable) => {
          return { ...variable, active: false };
        });
      }
      console.log(this.seriesVariables);
    },
    selectAll() {
      this.seriesVariables = this.seriesVariables.map((variable) => {
        return { ...variable, active: true };
      });
    },
    reset() {
      this.seriesVariables = [];
      this.active = [];
      this.isLoading = false;
    },
    async getSeriesVariables() {
      this.isLoading = true;
      await axios
        .request({
          url: "api/dataprovider/" + 6 + "/seriesvariables", //HARD CODED
          method: "get",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.seriesVariables = response.data;
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
        });
    },
    postVirtualStation() {
      this.isLoadingPost = true;
      let payload = {
        region_id: this.region.region_id,
        seriesvariables: this.seriesVariables,
      };
      console.log(payload);
      axios
        .request({
          url: "api/virtualdatasources",
          method: "post",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          console.log(response);
          this.isLoadingPost = false;
          this.generalStore.successSnackbar(
            "Estação virtual criada com sucesso"
          );
          this.$emit("refresh");
          this.dialog = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingPost = false;
          this.generalStore.errorSnackbar("Erro ao criar a estação virtual");
        });
    },
    async editVirtualStation() {
      this.isLoadingEdit = true;
      let payload = {
        datasource_name: this.region.virtual_datasource.datasource_name,
        seriesvariables: this.seriesVariables,
      };
      console.log(payload);
      await axios
        .request({
          url:
            "api/virtualdatasources/" +
            this.region.virtual_datasource.datasource_id,
          method: "patch",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingEdit = false;
          this.dialog = false;
          this.generalStore.successSnackbar(
            "Estação virtual editada com sucesso"
          );
          this.$emit("refresh");
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingEdit = false;
          this.generalStore.errorSnackbar("Erro ao editar a estação virtual");
        });
    },
  },
};
</script>

<style scoped>
.scrollable-card {
  max-height: 19rem;
  overflow-y: auto;
}
</style>
