<template>
  <v-card elevation="0" class="pa-5" height="90%" min-height="350">
    <v-card-title>
      <span class="text-h4">Observações na Parcela</span>
      <v-row class="mt-5">
        <v-col cols="5" class="d-flex align-center mb-5">
          <v-locale-provider locale="pt">
            <v-date-input
              v-model="filter.dates"
              placeholder="Datas"
              prepend-icon=""
              prepend-inner-icon="mdi-calendar"
              multiple="range"
              density="comfortable"
              variant="solo"
              hide-details
              clearable
              @click:clear="filter.dates = []"
            >
            </v-date-input>
            <v-menu :close-on-content-click="false">
              <template v-slot:activator="{ props }">
                <v-btn icon variant="plain" v-bind="props">
                  <v-icon>mdi-view-list</v-icon>
                  <v-tooltip activator="parent" location="top">
                    <span>Períodos Datas</span>
                  </v-tooltip>
                </v-btn>
              </template>
              <v-card min-width="250" max-height="300">
                <v-card-title>
                  <v-text-field
                    density="compact"
                    v-model="filter_periods.period_name"
                    label="Pesquisa o nome"
                    color="primary"
                    append-icon="mdi-magnify"
                    variant="underlined"
                    hide-details
                    clearable
                    @click:append="searchRegionInPeriods()"
                  ></v-text-field>
                </v-card-title>
                <v-list class="scrollable-menu">
                  <v-divider></v-divider>
                  <v-list-item
                    v-for="regioninperiod in regioninperiods"
                    :key="regioninperiod.regioninperiod_id"
                    :title="regioninperiod.period.period_name"
                    :subtitle="`${formatDate(
                      regioninperiod.period.period_startdate
                    )} - ${formatDate(regioninperiod.period.period_enddate)}`"
                    @click="updateDates(regioninperiod)"
                  ></v-list-item>
                  <v-list-item v-if="periods_hasMore">
                    <div v-intersect.quiet="loadMoreRegionsInPeriods">
                      <v-skeleton-loader type="list-item"></v-skeleton-loader>
                    </div>
                  </v-list-item>
                </v-list>
              </v-card>
            </v-menu>
          </v-locale-provider>
        </v-col>
        <v-col cols="2">
          <v-text-field
            v-model.trim="filter.nea"
            label="NEA"
            type="number"
            prefix="%"
            color="primary"
            density="comfortable"
            variant="solo"
            :rules="[rules.percentage]"
            clearable
          >
          </v-text-field>
        </v-col>
        <v-col cols="2">
          <v-autocomplete
            v-model="filter.ef"
            :loading="isLoadingStages"
            :items="stages"
            no-data-text="Sem resultados"
            item-title="phenologicalstage_code"
            item-value="phenologicalstage_code"
            label="EF"
            color="primary"
            density="comfortable"
            variant="solo"
            clearable
          >
            <template v-slot:item="{ props, item }">
              <v-list-item
                v-bind="props"
                :title="item.raw.phenologicalstage_code"
                :subtitle="item.raw.phenologicalstage_name"
              >
              </v-list-item>
            </template>
          </v-autocomplete>
        </v-col>
        <v-col align-self="stretch" cols="2" class="text-center">
          <v-menu :close-on-content-click="false">
            <template v-slot:activator="{ props }">
              <v-btn size="large" block variant="text" v-bind="props">
                <v-icon size="large">mdi-filter-plus-outline</v-icon>
                <v-tooltip activator="parent" location="top">
                  <span>Outros Filtros</span>
                </v-tooltip>
              </v-btn>
            </template>
            <v-list lines="one">
              <v-list-item
                ><v-switch
                  label="Visita"
                  v-model="filter.visited"
                  :indeterminate="filter.visited === null"
                  color="info"
                  prepend-icon="mdi-close-circle"
                  hide-details
                  @click:prepend="filter.visited = null"
                ></v-switch
              ></v-list-item>
              <v-divider></v-divider>
              <v-list-item>
                <v-switch
                  label="GPS"
                  v-model="filter.gps"
                  :indeterminate="filter.gps === null"
                  color="info"
                  prepend-icon="mdi-close-circle"
                  hide-details
                  @click:prepend="filter.gps = null"
                ></v-switch>
              </v-list-item>
              <v-divider></v-divider>
              <v-list-item>
                <v-switch
                  label="Fotos"
                  v-model="filter.hasPhotos"
                  :indeterminate="filter.hasPhotos === null"
                  color="info"
                  prepend-icon="mdi-close-circle"
                  hide-details
                  @click:prepend="filter.hasPhotos = null"
                ></v-switch>
              </v-list-item>
              <v-divider></v-divider>
              <v-list-item>
                <v-switch
                  label="Chuva"
                  v-model="filter.rain"
                  :indeterminate="filter.rain === null"
                  color="info"
                  prepend-icon="mdi-close-circle"
                  hide-details
                  @click:prepend="filter.rain = null"
                ></v-switch
              ></v-list-item>
              <v-divider></v-divider>
              <v-list-item>
                <v-switch
                  label="Tratamento"
                  v-model="filter.treatment"
                  :indeterminate="filter.treatment === null"
                  color="info"
                  prepend-icon="mdi-close-circle"
                  hide-details
                  @click:prepend="filter.treatment = null"
                ></v-switch
              ></v-list-item>
            </v-list>
          </v-menu>
        </v-col>
        <v-col>
          <v-btn
            color="secondary"
            icon
            variant="text"
            :disabled="isLoading"
            @click="searchVisits()"
          >
            <v-icon size="x-large">mdi-magnify</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-card-title>
    <v-card-text style="height: 100%">
      <v-card height="75%" class="pa-5" elevation="8" rounded>
        <v-card-title>
          <v-progress-linear
            v-if="isLoading"
            color="primary"
            indeterminate
          ></v-progress-linear>
        </v-card-title>
        <v-card-text class="scrollable">
          <v-row class="fill-width">
            <v-col v-if="visits.length == 0 && !isLoading">
              <v-card class="fill-width" elevation="0">
                <v-card-text class="text-center">
                  <v-icon size="100">mdi-file-search</v-icon>
                  <h2 class="text-h5">Nenhuma observação encontrada</h2>
                </v-card-text>
              </v-card>
            </v-col>
            <v-col v-else v-for="visit in visits" :key="visit.fileId" cols="6">
              <VisitCard
                :visit="visit"
                @detail="openDetail($event)"
              ></VisitCard>
            </v-col>
            <v-col v-if="vis_hasMore && !isLoading" cols="6">
              <v-skeleton-loader
                v-intersect.quiet="{
                  handler: loadMoreVisits,
                  options: {
                    threshold: 0.3,
                  },
                }"
                type="card"
              ></v-skeleton-loader>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
      <ObservationDetail
        :visit="selected_visit"
        :dialog="detailDialog"
        @update:dialog="detailDialog = $event"
      ></ObservationDetail>
    </v-card-text>
  </v-card>
</template>

<script>
import VisitCard from "@/components/Visits/VisitCard.vue";
import ObservationDetail from "@/components/Visits/VisitDetail.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "RegionVisitsDetails",
  props: {
    region: Object,
  },
  components: {
    VisitCard,
    ObservationDetail,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      //list observations
      isLoading: false,
      observations: [],

      visits: [],
      vis_offset: 0,
      vis_limit: 8,
      vis_hasMore: false,

      //detail dialog
      detailDialog: false,
      selected_visit: null,

      //pheno stages for filter
      stages: [],
      isLoadingStages: false,

      //regioninperiods for filter
      regioninperiods: [],
      isLoadingPeriods: false,
      filter_periods: {
        period_name: null,
      },
      periods_offset: 0,
      periods_limit: 5,
      periods_hasMore: false,

      //filter
      filter: {
        dates: [],
        email: null,
        fileId: null,
        nea: null,
        ef: null,
        visited: null,
        gps: null,
        hasPhotos: null,
        numPhotos: null,
        rain: null,
        treatment: null,
      },

      rules: {
        percentage: (value) => {
          if (value === null || value === "") return true;
          return (value >= 0 && value <= 100) || "Valor inválido";
        },
        gtZero: (value) => {
          if (value === null || value === "") return true;
          return value > 0 || "Valor inválido";
        },
      },
    };
  },
  computed: {
    dateRange() {
      return this.filter.dates.length > 0
        ? this.filter.dates[0] +
            " - " +
            this.filter.dates[this.filter.dates.length - 1]
        : null;
    },
  },
  methods: {
    handleClick(event) {
      console.log(event);
    },
    openDetail(visit) {
      this.selected_visit = JSON.parse(JSON.stringify(visit));
      this.detailDialog = true;
    },
    async listObservations() {
      this.isLoading = true;
      await axios
        .request({
          url: `api/observations`,
          method: "GET",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
          params: {
            region_code: this.region.region_code,
            date_range: this.dateRange,
            nea: this.filter.nea,
            ef: this.filter.ef,
            visited: this.filter.visited,
            gps: this.filter.gps,
            hasPhotos: this.filter.hasPhotos,
            rain: this.filter.rain,
            treatment: this.filter.treatment,
          },
        })
        .then((response) => {
          this.observations = response.data;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao carregar observações");
        })
        .finally(() => {
          this.isLoading = false;
        });
    },

    async listVisits() {
      this.isLoading = true;
      await axios
        .request({
          url: `api/visits`,
          method: "GET",
          headers: {
            Authorization: `Bearer ${this.accountStore.account.token}`,
          },
          params: {
            offset: this.vis_offset,
            limit: this.vis_limit,

            region_code: this.region.region_code,
            date_range: this.dateRange,
            nea: this.filter.nea,
            ef: this.filter.ef,
            visited: this.filter.visited,
            gps: this.filter.gps,
            hasPhotos: this.filter.hasPhotos,
            rain: this.filter.rain,
            treatment: this.filter.treatment,
          },
        })
        .then((response) => {
          this.visits.push(...response.data.visits);
          this.vis_offset += response.data.visits.length;
          this.vis_hasMore = response.data.hasMore;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar("Erro ao carregar visitas");
        })
        .finally(() => {
          this.isLoading = false;
        });
    },

    searchVisits() {
      this.visits = [];
      this.vis_offset = 0;
      this.listVisits();
    },

    loadMoreVisits(isIntersecting) {
      if (isIntersecting) {
        this.listVisits();
      }
    },

    listPhenologicalStages() {
      this.isLoadingStages = true;
      axios
        .request({
          url: "api/phenologicalstages",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.stages = response.data;
          this.isLoadingStages = false;
        })
        .catch((error) => {
          console.error(error);
          this.generalStore.errorSnackbar(
            "Erro ao carregar estágios fenológicos"
          );
        });
    },
    listRegioninPeriods() {
      this.isLoadingPeriods = true;
      axios
        .request({
          url: "api/regions/" + this.region.region_id + "/regionsinperiods",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            limit: this.periods_limit,
            offset: this.periods_offset,

            period_name: this.filter_periods.period_name,
          },
        })
        .then((response) => {
          this.regioninperiods.push(...response.data.regioninperiods);
          this.periods_offset += response.data.regioninperiods.length;
          this.periods_hasMore = response.data.hasMore;
          this.isLoadingPeriods = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingPeriods = false;
          this.generalStore.errorSnackbar("Erro ao carregar períodos");
        });
    },
    loadMoreRegionsInPeriods(isIntersecting) {
      if (isIntersecting) {
        this.listRegioninPeriods();
      }
    },

    searchRegionInPeriods() {
      this.regioninperiods = [];
      this.periods_offset = 0;
      this.listRegioninPeriods();
    },

    formatDate(date) {
      return new Date(date).toLocaleDateString("pt-PT");
    },

    updateDates(regioninperiod) {
      const start_date = new Date(regioninperiod.period.period_startdate);
      const end_date = new Date(regioninperiod.period.period_enddate);
      this.filter.dates = [start_date, end_date];
    },
  },
  mounted() {
    this.listRegioninPeriods();
    this.listPhenologicalStages();
    this.listVisits();
    // this.listObservations();
  },
  unmounted() {
    this.observations = [];
  },
};
</script>

<style scoped>
.scrollable {
  overflow-y: auto;
  max-height: 100%;
}
.scrollable-menu {
  max-height: 220px;
  overflow-y: auto;
}
</style>
