<template>
  <v-dialog v-model="localDialog" @afterLeave="reset()" width="auto">
    <v-card class="mx-auto" width="500">
      <v-card-title class="bg-warning d-flex align-center"
        ><v-icon size="small" class="mr-2">mdi-pencil</v-icon
        ><span>Editar Parcela</span></v-card-title
      >
      <v-card-text>
        <v-skeleton-loader
          v-if="isLoadingGet"
          type="text"
          class="mb-4"
        ></v-skeleton-loader>
        <v-form v-else v-model="formValid" ref="form">
          <v-row>
            <!-- <v-col cols="8">
              <v-text-field
                v-model="region.region_name"
                label="Nome"
                placeholder="Insira o nome da parcela"
                color="secondary"
                density="compact"
                variant="outlined"
                :rules="[rules.required, rules.maxLength]"
              ></v-text-field>
            </v-col>
            <v-col cols="4">
              <v-text-field
                v-model="region.region_code"
                label="Código"
                placeholder="Insira o código da parcela"
                color="secondary"
                density="compact"
                variant="outlined"
                :rules="[rules.required, rules.code]"
              ></v-text-field>
            </v-col> -->
            <v-col cols="6">
              <v-autocomplete
                v-model="region.crop"
                :items="crops"
                item-title="crop_name"
                return-object
                label="Cultura"
                placeholder="Escolhe uma cultura"
                prepend-inner-icon="mdi-tree-outline"
                no-data-text="Nenhuma cultura encontrada"
                density="compact"
                variant="outlined"
                :rules="[rules.required]"
                :loading="isLoadingCrops"
                @update:modelValue="refreshVarieties()"
              ></v-autocomplete>
            </v-col>
            <v-col cols="6">
              <v-autocomplete
                v-model="region.variety"
                :items="varieties"
                item-title="variety_name"
                return-object
                label="Variedade"
                placeholder="Escolhe uma variedade"
                prepend-inner-icon="mdi-food-apple-outline"
                no-data-text="Nenhuma variedade encontrada"
                density="compact"
                variant="outlined"
                :rules="[rules.required]"
                :disabled="!region.crop"
                :loading="isLoadingVarieties"
              ></v-autocomplete>
            </v-col>
            <!-- <v-col cols="6">
              <v-autocomplete
                v-model="this.region.closestdatasource_id"
                :items="stations"
                item-title="datasource_name"
                item-value="datasource_id"
                label="Estação meteorológica"
                placeholder="Escolhe uma estação"
                no-data-text="Nenhuma estação encontrada"
                color="secondary"
                density="compact"
                variant="outlined"
                :disabled="isLoadingStations"
              >
                <template v-slot:item="{ props, item }">
                  <v-list-item
                    v-bind="props"
                    :subtitle="convertToKm(item.raw.distance)"
                  >
                  </v-list-item>
                </template>
              </v-autocomplete>
            </v-col> -->
            <v-col cols="12">
              <v-textarea
                v-model="region.region_description"
                label="Descrição"
                placeholder="Insira uma descrição..."
                prepend-inner-icon="mdi-text-box-outline"
                density="compact"
                variant="outlined"
                :rules="[rules.description]"
              ></v-textarea>
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>
      <v-divider></v-divider>
      <v-card-actions class="d-flex justify-space-around">
        <v-btn
          color="error"
          variant="tonal"
          icon="mdi-close"
          @click="localDialog = false"
        ></v-btn>
        <v-btn
          color="secondary"
          variant="tonal"
          icon="mdi-check"
          :loading="isLoadingEdit"
          :disabled="!canEdit"
          @click="editRegion()"
        ></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";
export default {
  name: "RegionEdit",
  props: {
    regionProp: {
      type: Object,
    },
    dialog: {
      type: Boolean,
      required: true,
    },
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();

    return { generalStore, accountStore };
  },
  data() {
    return {
      oldRegion: null,
      region: null,
      isLoadingEdit: false,
      isLoadingGet: false,
      formValid: false,

      // Organization members
      orgMembers: [],
      isLoadingMembers: false,

      //nearest stations
      stations: [],
      isLoadingStations: false,

      //crops
      crops: [],
      isLoadingCrops: false,

      //varieties
      varieties: [],
      isLoadingVarieties: false,

      rules: {
        required: (v) => !!v || "Campo obrigatório.",
        code: (v) =>
          (v && v.length <= 3 && v.toUpperCase() === v) || "Código inválido",
        maxLength: (v) => (v && v.length <= 25) || "Máximo de 25 caracteres.",
        description: (v) =>
          (v && v.length <= 255) || !v || "Máximo de 255 caracteres.",
      },
    };
  },
  watch: {
    dialog(value) {
      if (value) {
        this.onOpenDialog();
      }
    },
  },
  computed: {
    localDialog: {
      get() {
        return this.dialog;
      },
      set(value) {
        this.$emit("update:dialog", value);
      },
    },
    regionCenter() {
      const cordinates = this.region.geometry.geometry_shape.coordinates[0];
      const sum = cordinates.reduce(
        (acc, [lng, lat]) => [acc[0] + lng, acc[1] + lat],
        [0, 0]
      );
      const center = [sum[0] / cordinates.length, sum[1] / cordinates.length];
      return center;
    },
    isDifferent() {
      return (
        this.region?.crop?.crop_id != this.oldRegion?.crop?.crop_id ||
        this.region?.variety?.variety_id !=
          this.oldRegion?.variety?.variety_id ||
        this.region?.region_description != this.oldRegion?.region_description
        // ||
        // this.region?.closestdatasource_id !=
        //   this.oldRegion?.closestdatasource_id
      );
    },
    canEdit() {
      return this.formValid && this.isDifferent;
    },
  },
  methods: {
    async onOpenDialog() {
      await this.getRegionDetail();
      // await this.getNearestStations();
      await this.getCrops();
      if (this.region.crop) await this.getVarieties();
      // if (this.accountStore.hasRole(["Organização"])) this.getOrgMembers();
    },

    editRegion() {
      this.isLoadingEdit = true;
      let payload = {
        crop: { ...this.region.crop, variety: this.region.variety },
        region_description: this.region.region_description,
        // closestdatasource_id: this.region.closestdatasource_id,
      };
      console.log(payload);
      axios
        .request({
          url: "api/regions/" + this.regionProp.region_id,
          method: "patch",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingEdit = false;
          this.localDialog = false;
          this.generalStore.successSnackbar("Parcela editada com sucesso.");
          this.$emit("refresh");
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingEdit = false;
          this.generalStore.errorSnackbar("Erro ao editar parcela.");
        });
    },
    async getOrgMembers() {
      this.isLoadingMembers = true;
      await axios
        .request({
          url:
            "api/organizations/" +
            this.regionProp.organization_id +
            "/organizationproducers",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.orgMembers = response.data;
          this.isLoadingMembers = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingMembers = false;
          this.generalStore.errorSnackbar("Erro ao obter produtores.");
        });
    },
    async getNearestStations() {
      this.isLoadingStations = true;
      let payload = {
        location: this.regionCenter,
      };
      await axios
        .request({
          url: "api/closedatasources",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: payload,
        })
        .then((response) => {
          this.stations = response.data.datasources;
          this.isLoadingStations = false;
        });
    },
    async getCrops() {
      this.isLoadingCrops = true;
      await axios
        .request({
          url: "api/crops",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.crops = response.data;
          this.isLoadingCrops = false;
        })
        .catch((error) => {
          this.isLoadingCrops = false;
          this.generalStore.errorSnackbar("Erro ao obter culturas.");
        });
    },
    async refreshVarieties() {
      this.region.variety = null;
      await this.$refs.form.validate();
      this.getVarieties();
    },
    async getVarieties() {
      this.isLoadingVarieties = true;
      await axios
        .request({
          url: "api/crops/" + this.region.crop.crop_id + "/varieties",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.varieties = response.data;
          this.isLoadingVarieties = false;
        })
        .catch((error) => {
          this.isLoadingVarieties = false;
          this.generalStore.errorSnackbar("Erro ao obter variedades.");
        });
    },
    async getRegionDetail() {
      this.isLoadingGet = true;
      await axios
        .request({
          url: "api/regions/" + this.regionProp.region_id,
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.region = response.data;
          this.oldRegion = { ...response.data };
          console.log(response.data);
          // this.region.crop = response.data.region_properties.crop;
          this.isLoadingGet = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    convertToKm(distance) {
      return (distance / 1000).toFixed(2) + " km";
    },
    reset() {
      this.region = null;
      this.orgMembers = [];
    },
  },
};
</script>

<style></style>
