<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn color="success" icon="mdi-plus" v-bind="activatorProps"> </v-btn>
    </template>
    <template v-slot:default="{}">
      <v-card class="mx-auto" min-width="400">
        <v-card-title class="bg-success">{{ steps[step].title }}</v-card-title>
        <v-window v-model="step"
          ><v-window-item :value="0">
            <v-card-text>
              <v-form v-model="steps[0].formValid">
                <!-- <span class="text-subtitle-1 text-medium-emphasis">
                  Cargo do Utilizador*
                </span> -->
                <v-select
                  class="mt-4"
                  v-model="roleSelected"
                  :items="roles"
                  item-title="title"
                  item-value="value"
                  label="Cargo do Utilizador"
                  placeholder="Escolha o cargo do utilizador"
                  prepend-inner-icon="mdi-account-cog-outline"
                  no-data-text="Nenhum cargo encontrado"
                  density="compact"
                  variant="outlined"
                  :rules="[(v) => !!v || 'Campo obrigatório']"
                  :loading="isLoadingAgents"
                ></v-select>
              </v-form>
            </v-card-text>
          </v-window-item>
          <v-window-item :value="1">
            <v-card-text>
              <v-form v-model="steps[1].formValid">
                <!-- <span class="text-subtitle-1 text-medium-emphasis">
                  Utilizador*
                </span> -->
                <v-autocomplete
                  class="mt-4"
                  v-model="agent"
                  :items="selectItemsAgents"
                  item-title="agent_username"
                  prepend-inner-icon="mdi-account-outline"
                  return-object
                  :label="
                    roleSelected === 'Produtor' ? 'Produtor' : 'Agente de Campo'
                  "
                  :placeholder="
                    roleSelected === 'Produtor'
                      ? 'Escolha o produtor'
                      : 'Escolha o agente de campo'
                  "
                  :no-data-text="
                    roleSelected === 'Produtor'
                      ? 'Nenhum produtor encontrado'
                      : 'Nenhum agente de campo encontrado'
                  "
                  density="compact"
                  variant="outlined"
                  :rules="[(v) => !!v || 'Campo obrigatório']"
                  :disabled="isLoadingAgents"
                >
                  <template v-slot:item="{ props, item }">
                    <v-list-item v-bind="props" :subtitle="item.raw.agent_name">
                    </v-list-item>
                  </template>
                </v-autocomplete>
              </v-form>
            </v-card-text>
          </v-window-item>
          <v-window-item :value="3">
            <v-form v-model="steps[2].formValid">
              <v-locale-provider locale="pt">
                <v-card-text>
                  <div class="mt-4">
                    <v-date-input
                      v-model="startDate"
                      label="Data de Início"
                      placeholder="dd/mm/aaaa"
                      prepend-icon=""
                      prepend-inner-icon="mdi-calendar-end"
                      density="compact"
                      variant="outlined"
                      :rules="[(v) => !!v || 'Campo obrigatório']"
                    ></v-date-input>
                  </div>
                </v-card-text>
              </v-locale-provider>
            </v-form>
          </v-window-item>
        </v-window>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn
            v-if="step > 0"
            prepend-icon="mdi-chevron-left"
            color="info"
            variant="tonal"
            @click="step--"
          >
            Anterior
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            v-if="!lastStep"
            :disabled="!steps[step].formValid"
            append-icon="mdi-chevron-right"
            color="info"
            variant="tonal"
            @click="step++"
          >
            Próximo
          </v-btn>
          <v-btn
            v-else
            :disabled="!steps[step].formValid"
            append-icon="mdi-check"
            color="success"
            variant="flat"
            :loading="isLoadingPost"
            @click="postAgentRegion()"
          >
            Adicionar
          </v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";
export default {
  name: "AddRegionAgent",
  props: {
    region: Object,
    existingAgents: Array,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();

    return { accountStore, generalStore };
  },
  data() {
    return {
      dialog: false,
      agents: [],
      isLoadingAgents: false,
      agent: null,
      startDate: new Date(),
      isLoadingPost: false,
      step: 0,
      steps: [
        {
          title: "Cargo do utilizador",
          icon: "mdi-account",
          formValid: false,
        },
        { title: "Utilizador", icon: "mdi-account", formValid: false },
        { title: "Data de Início", icon: "mdi-calendar", formValid: false },
      ],
      roles: [
        { title: "Proprietário", value: "Produtor" },
        { title: "Agente de Campo", value: "Agente de Campo" },
      ],
      roleSelected: null,
    };
  },
  watch: {
    roleSelected(value) {
      if (value) {
        this.agent = null;
        this.listAgents();
      }
    },
  },
  computed: {
    lastStep() {
      return this.step === this.steps.length - 1;
    },
    selectItemsAgents() {
      return this.agents.filter(
        (agent) => !this.existingAgentIds.includes(agent.agent_id)
      );
    },
    existingAgentIds() {
      return this.existingAgents.map((existingAgent) => existingAgent.agent.id);
    },
    startDateFormatted() {
      return this.startDate.toLocaleDateString("pt-PT");
    },
  },
  methods: {
    async listAgents() {
      this.isLoadingAgents = true;
      let url =
        this.roleSelected === "Produtor"
          ? "api/organizations/" +
            this.region.organization.organization_id +
            "/organizationproducers"
          : "api/organizations/" +
            this.region.organization.organization_id +
            "/organizationfieldagents";
      await axios
        .request({
          url: url,
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.agents = response.data;
          this.isLoadingAgents = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingAgents = false;
        });
    },
    async postAgentRegion() {
      this.isLoadingPost = true;
      let payload = {
        region_id: this.region.region_id,
        agent_id: this.agent.agent_id,
        startDate: this.startDateFormatted,
      };
      await axios
        .request({
          url: "api/agentsregions",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingPost = false;
          this.dialog = false;
          this.generalStore.successSnackbar(
            "Utilizador adicionado com sucesso."
          );
          this.$emit("refresh");
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingPost = false;
          this.generalStore.errorSnackbar("Erro ao adicionar agente.");
        });
    },
    reset() {
      this.step = 0;
      this.roleSelected = null;
      this.agents = [];
      this.agent = null;
    },
  },
};
</script>

<style></style>
