<template>
  <v-navigation-drawer
    location="right"
    v-model="drawer"
    width="1000"
    temporary
    disable-route-watcher
  >
    <v-card class="fill-height">
      <v-card-title class="bg-primary white--text">
        <span class=""
          >Detalhes da Parcela <i>{{ region?.region_name }}</i></span
        >
      </v-card-title>
      <v-card-text class="fill-height pa-0">
        <v-container class="fill-height pa-0">
          <v-row v-if="isLoading" align="center">
            <v-col class="d-flex align-center justify-center">
              <v-progress-circular
                color="secondary"
                size="65"
                width="7"
                indeterminate
              ></v-progress-circular>
            </v-col>
          </v-row>
          <v-card
            v-if="!isLoading && region != null"
            elevation="0"
            flat
            rounded
            width="100%"
            height="100%"
            class="ma-0"
          >
            <v-tabs color="secondary" v-model="tab" grow>
              <v-tab value="region"> Parcela </v-tab>
              <v-tab value="eois"> EOIs </v-tab>
              <v-tab value="regioninperiods"> Períodos</v-tab>
              <!-- <v-tab value="regiondatasources"> Estações </v-tab> -->
              <v-tab value="agents">Utilizadores</v-tab>
              <v-tab value="visits">Visitas</v-tab>
            </v-tabs>
            <v-card-text class="fill-height">
              <v-tabs-window v-model="tab" class="fill-height">
                <v-tabs-window-item value="region" class="fill-height">
                  <RegionDetail
                    :region="region"
                    @refresh="getRegionDetail(region.region_id)"
                  ></RegionDetail>
                </v-tabs-window-item>
                <v-tabs-window-item value="eois" class="fill-height">
                  <EOIsDetail ref="EOIsDetail" :region="region"></EOIsDetail>
                </v-tabs-window-item>
                <v-tabs-window-item value="regioninperiods" class="fill-height">
                  <PeriodsDetail
                    :region="region"
                    @refreshEOIs="refreshEOIs"
                  ></PeriodsDetail>
                </v-tabs-window-item>
                <v-tabs-window-item
                  value="regiondatasources"
                  class="fill-height"
                >
                  <DatasourcesDetail :region="region"></DatasourcesDetail>
                </v-tabs-window-item>
                <v-tabs-window-item value="agents" class="fill-height">
                  <RegionAgentsDetail :region="region"></RegionAgentsDetail>
                </v-tabs-window-item>
                <v-tabs-window-item value="visits" class="fill-height">
                  <RegionVisitsDetails
                    :region="region"
                  ></RegionVisitsDetails>
                </v-tabs-window-item>
              </v-tabs-window>
            </v-card-text>
          </v-card>
        </v-container>
      </v-card-text>
    </v-card>
  </v-navigation-drawer>
</template>

<script>
import RegionDetail from "./RegionDetail.vue";
import EOIsDetail from "./EOIsDetail.vue";
import PeriodsDetail from "./PeriodsDetail.vue";
import DatasourcesDetail from "./DatasourcesDetail.vue";
import RegionAgentsDetail from "./RegionAgentsDetail.vue";
import RegionVisitsDetails from "./RegionVisitsDetails.vue";

import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";
export default {
  name: "RegionDrawer",
  components: {
    RegionDetail,
    EOIsDetail,
    PeriodsDetail,
    DatasourcesDetail,
    RegionAgentsDetail,
    RegionVisitsDetails,
  },
  setup() {
    const accountStore = useAccountStore();
    return { accountStore };
  },
  data() {
    return {
      drawer: false,
      tab: null,
      isLoading: false,
      region: null,
    };
  },
  watch: {
    drawer(value) {
      if (!value) {
        this.reset();
      }
    },
  },
  methods: {
    openDrawer(region_id) {
      this.drawer = true;
      this.getRegionDetail(region_id);
    },
    async getRegionDetail(region_id) {
      this.isLoading = true;
      await axios
        .request({
          url: "api/regions/" + region_id,
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.region = response.data;
          this.isLoading = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    reset() {
      this.region = null;
      this.isLoading = false;
      this.tab = null;
    },
    refreshEOIs() {
      console.log("refreshEOIs");
      this.$refs.EOIsDetail?.refreshEOIs();
    },
  },
};
</script>

<style></style>
