<template>
  <v-sheet class="pa-2" border rounded="lg" :height="height">
    <MapboxMap
      ref="mapboxMap"
      :accessToken="accessToken"
      mapStyle="mapbox://styles/mapbox/satellite-streets-v12"
      @mb-created="(mapInstance) => (map = mapInstance)"
      :center="mapCenter"
      :zoom="zoom"
      :style="{ height: '100%' }"
    ></MapboxMap>
  </v-sheet>
</template>

<script>
import { MapboxMap } from "@studiometa/vue-mapbox-gl";

export default {
  name: "PointMap",
  components: { MapboxMap },
  props: {
    point: {
      type: Object,
      default: () => ({
        type: "Point",
        coordinates: [0, 0],
      }),
    },
    zoom: {
      type: Number,
      default: 16,
    },
    height: {
      type: String,
      default: "100%",
    },
    width: {
      type: String,
      default: "100%",
    },
  },
  setup() {
    const accessToken = process.env.VUE_APP_MAPBOX_TOKEN;

    return { accessToken };
  },
  data() {
    return {
      map: null,
    };
  },
  watch: {
    point: {
      handler(point) {
        if (this.map) {
          this.map.getSource("point").setData({
            type: "Feature",
            geometry: point,
          });
        }
      },
      deep: true,
    },
  },
  computed: {
    mapCenter() {
      return this.point.coordinates;
    },
  },
  mounted() {
    this.map.on("load", () => {
      this.map.addSource("point", {
        type: "geojson",
        data: {
          type: "Feature",
          geometry: this.point,
        },
      });
      this.map.addLayer({
        id: "point",
        type: "circle",
        source: "point",
        paint: {
          "circle-radius": 6,
          "circle-color": "#880",
          "circle-stroke-width": 2,
          "circle-stroke-color": "#fff",
        },
      });
    });
  },
};
</script>

<style></style>
