<template>
  <v-container fluid class="fill-width">
    <v-row>
      <v-col cols="12">
        <v-card elevation="8">
          <v-card-title>Filtro</v-card-title>
          <v-card-text>
            <v-row>
              <v-col cols="6"
                ><v-row>
                  <v-col cols="3">
                    <v-text-field
                      clearable
                      density="comfortable"
                      variant="underlined"
                      color="primary"
                      label="Código da Parcela"
                      :disabled="isLoadingRegions"
                      v-model="filter.region_code"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="4">
                    <v-text-field
                      clearable
                      density="comfortable"
                      variant="underlined"
                      color="primary"
                      label="Nome da Parcela"
                      :disabled="isLoadingRegions"
                      v-model="filter.region_name"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="4">
                    <v-autocomplete
                      clearable
                      v-model="filter.regiontype_id"
                      label="Tipo de Região"
                      :items="regiontypes"
                      item-title="regiontype_name"
                      item-value="regiontype_id"
                      density="comfortable"
                      variant="underlined"
                      color="primary"
                      :disabled="isLoadingRegionTypes"
                    ></v-autocomplete>
                  </v-col>
                  <v-col cols="3">
                    <v-text-field
                      clearable
                      density="comfortable"
                      variant="underlined"
                      color="primary"
                      label="Localidade"
                      :disabled="isLoadingRegions"
                      v-model="filter.local"
                    ></v-text-field>
                  </v-col>
                  <v-col cols="3">
                    <v-autocomplete
                      clearable
                      density="comfortable"
                      variant="underlined"
                      color="primary"
                      label="Cultura"
                      :disabled="isLoadingCrops"
                      :items="crops"
                      item-title="crop_name"
                      item-value="crop_id"
                      v-model="filter.crop"
                    ></v-autocomplete>
                  </v-col>
                  <v-col
                    v-if="accountStore.hasRole(['Administrador'])"
                    cols="4"
                  >
                    <v-autocomplete
                      clearable
                      density="comfortable"
                      variant="underlined"
                      color="primary"
                      label="Organização"
                      :disabled="isLoadingRegions || isLoadingOrganizations"
                      :items="organizations"
                      item-title="organization_name"
                      item-value="organization_id"
                      v-model="filter.organization_id"
                    ></v-autocomplete>
                  </v-col> </v-row
              ></v-col>
              <v-col cols="3"
                ><v-row>
                  <v-col cols="12">
                    <v-btn
                      color="secondary"
                      icon
                      variant="text"
                      @click="search()"
                      :disabled="isLoadingRegions"
                    >
                      <v-icon size="x-large">mdi-magnify</v-icon>
                    </v-btn>
                  </v-col></v-row
                ></v-col
              >
              <v-col cols="3"
                ><v-row>
                  <v-col cols="12">
                    <AddRegion
                      v-if="accountStore.hasRole(['Produtor', 'Organização'])"
                      @refreshRegions="search()"
                    ></AddRegion> </v-col></v-row
              ></v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <RegionsDetail ref="RegionDetail"></RegionsDetail>
    <v-row>
      <v-col>
        <RegionsTable
          :regions="regions"
          :isLoadingRegions="isLoadingRegions"
          :hasMore="hasMore"
          @loadMore="getRegions()"
          @showInfo="showInfo($event)"
          @refresh="search()"
        ></RegionsTable>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import RegionsTable from "../components/Regions/RegionsTable.vue";
import RegionsDetail from "../components/Regions/RegionDrawer.vue";
import AddRegion from "@/components/Regions/AddRegion/AddRegion.vue";

import { useAccountStore } from "@/stores/accountStore";
import axios from "axios";

export default {
  name: "RegionsView",
  components: {
    RegionsTable,
    RegionsDetail,
    AddRegion,
  },
  setup() {
    const accountStore = useAccountStore();
    return { accountStore };
  },
  data() {
    return {
      regiontypes: [],
      isLoadingRegionTypes: false,

      organizations: [],
      isLoadingOrganizations: false,

      crops: [],
      isLoadingCrops: false,

      isLoadingRegions: false,
      isLoadingDetail: false,
      drawer: false,
      selectedRegion: null,

      regions: [],
      filter: {
        region_code: null,
        region_name: null,
        regiontype_id: null,
        local: null,
        crop: null,
        organization_id: null,
      },
      limit: 12,
      offset: 0,
      hasMore: false,
    };
  },
  watch: {},
  computed: {},
  methods: {
    async getRegionTypes() {
      this.isLoadingRegionTypes = true;
      await axios
        .request({
          url: "api/regiontypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.regiontypes = response.data;
          this.isLoadingRegionTypes = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingRegionTypes = false;
          this.generalStore.errorSnackbar("Erro ao obter tipos de região.");
        });
    },
    async getOrganizations() {
      this.isLoadingOrganizations = true;
      await axios
        .request({
          url: "api/organizations",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.organizations = response.data.organizations;
          this.isLoadingOrganizations = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async getCrops() {
      this.isLoadingCrops = true;
      await axios
        .request({
          url: "api/crops",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.crops = response.data;
          this.isLoadingCrops = false;
        })
        .catch((error) => {
          this.isLoadingCrops = false;
          this.generalStore.errorSnackbar("Erro ao obter culturas.");
        });
    },
    async getRegions() {
      this.isLoadingRegions = true;
      await axios
        .request({
          url: "api/regions",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            region_code: this.filter.region_code,
            region_name: this.filter.region_name,
            regiontype_id: this.filter.regiontype_id,
            local: this.filter.local,
            crop: this.filter.crop,
            organization_id: this.filter.organization_id,
            //pagination
            limit: this.limit,
            offset: this.offset,
          },
        })
        .then((response) => {
          this.regions.push(...response.data.regions);
          this.offset += response.data.regions.length;
          this.hasMore = response.data.hasMore;
          this.isLoadingRegions = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingRegions = false;
        });
    },
    async getRegionDetail(region_id) {
      this.isLoadingDetail = true;
      await axios
        .request({
          url: "api/regions/" + region_id,
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.selectedRegion = response.data;
          this.isLoadingDetail = false;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    search() {
      this.offset = 0;
      this.regions = [];
      this.getRegions();
    },
    showInfo(region_id) {
      this.$refs.RegionDetail.openDrawer(region_id);
      // this.selectedRegion = region;
      // this.drawer = true;
    },
  },
  mounted() {
    this.getRegionTypes();
    this.getRegions();
    this.getCrops();
    if (this.accountStore.hasRole(["Administrador"])) this.getOrganizations();
  },
};
</script>

<style></style>
